import React from "react";
import { DebounceInput } from "react-debounce-input";
import BuscarAsignacionesHook from "../../../../hooks/buscar_asignaciones_hook";
import EstructuraManifiesto from "../../../dash-componets/estructura_manifiesto/estructura-manifiesto";
import Spinner from "../../../spinner/spinner";
import Dashfooter from "../../../dash-componets/footter/dashfooter";

function BuscarAsignacion() {
  const [buscarAsignacionMethod, cleanAsignacion,values, fetching, msgError, currentAsignacion] =
    BuscarAsignacionesHook();
  return (
    <div>
      <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4 d-print-none scroll-section">
        <div className="container-fluid pt-4 mb-4 ">
          <h4 className="title-separtor">
            Buscar Asignación de Correspondencia
          </h4>
          <hr />
          <div className="card card-body">
            <div className="row align-items-center mb-4">
              <div className="col-sm-12 col-md-12">
                <form>
                  <label className="form-label-input">Buscar Asignacion</label>
                  <div className="input-group">
                    <DebounceInput
                      minLength={4}
                      debounceTimeout={500}
                      type="text"
                      className="form-control dropdown-toggle"
                      placeholder="Num. Asignación..."
                      
                      name="searchMan"
                      value={values?.textSearch}
                      onChange={buscarAsignacionMethod}
                    />
                    <span className="mdi mdi-magnify search-icon" />
                    {fetching ? (
                      <div className="container-spinner-buscar">
                        <Spinner />
                      </div>
                    ) : (
                      <button
                        className="btn-solid-secondary px-4 "
                        type="button"
                      >
                        Buscar
                      </button>
                    )}
                  </div>
                </form>
              </div>
            </div>
          </div>

          <div className="card card-body">
            {currentAsignacion === undefined? (
              <p>Esperando Contenido de Asignacion</p>
            ) : (
              <div>
                <div className="w-100 text-end">
                  <button
                    type="button"
                    className="btn-close p-2"
                    onClick={cleanAsignacion}
                  ></button>
                </div>
                <EstructuraManifiesto
                  asignacion={currentAsignacion}
                  last={true}
                  showmarca={false}
                />
                <button
                  type="button"
                  onClick={() => window.print()}
                  className="btn btn-success w-100"
                >
                  Imprimir Asignación
                </button>
              </div>
            )}
          </div>
        </div>
        <Dashfooter />
      </div>
      <div className="col-md-9 ms-sm-auto col-lg-10 d-none d-print-block">
        {currentAsignacion === undefined? (
          <></>
        ) : (
          <EstructuraManifiesto asignacion={currentAsignacion} last={true} />
        )}
      </div>
    </div>
  );
}

export default BuscarAsignacion;
