import React, { useCallback, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  actualizarRol,
  consultarRoles,
} from "../../../../redux/actions/roles_actions";
import "../asignacion_acciones/asignacion-acciones.css";
import { useToasts } from "react-toast-notifications";
import AsignacionHook from "../../../../hooks/asignacion_acciones_hook";

function AsignacionAcciones() {
  const [
	user,
    fetching,
    funciones,
    listRoles,
    switchRol,
    actualizarAsignacion,
    changeValue,
    handleChange,
    buscar,
  ] = AsignacionHook();
  
  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4">
      <div className="container-fluid pt-4 mb-4 ">
        <h4 className="title-separtor">Asignacion Acciones</h4>
        <hr />
        <div className="card card-body cart-info-data">
          <div className="row ">
            <div className="col-lg-6 ">
              <form>
                <div className="mb-3">
                  <label className="form-label-input">Tipo Usuario</label>
                  <select
                    className="form-select"
                    value={user}
                    onChange={changeValue}
                  >
                    <option value="">Seleccionar Tipo de</option>
                    {listRoles?.map((rol) => (
                      <option key={rol.id} value={rol.rol}>
                        {switchRol( rol.rol)}
                      </option>
                    ))}
                  </select>
                </div>
              </form>
            </div>
          </div>
          {user == "" ? (
            <div className="p-4">Debes seleccionar un Rol</div>
          ) : (
            <div  >
              <h5 className="titleSection-form">Acciones</h5>
              <hr />
              <div className="row mt-2 p-4">
                <div className="col-lg-3 ">
                  <label className="form-label-input">Guias</label>
                  <div className="mt-3">
				  <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="guia_crear"
                        checked={funciones?.guia_crear}
                        onChange={() => handleChange("guia_crear")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Crear Guias
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="guia_rastrear"
                        checked={funciones?.guia_rastrear}
                        onChange={() => handleChange("guia_rastrear")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Rastrear Guias
                      </label>
                    </div>
                  
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="guia_anular"
                        checked={funciones?.guia_anular}
                        onChange={() => handleChange("guia_anular")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Anular Guias
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        id="customCheck5"
                        name="guia_solicitar_anular"
                        checked={funciones?.guia_solicitar_anular}
                        onChange={() => handleChange("guia_solicitar_anular")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Solicitar Anulación
                      </label>
                    </div>
                  </div>
                </div>
                <div className="col-lg-3 ">
                  <label className="form-label-input">Manifiestos</label>
                  <div className="mt-3">
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="manifiesto_actualizar_peso"
                        checked={funciones?.manifiesto_actualizar_peso}
                        onChange={() => handleChange("manifiesto_actualizar_peso")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Actualizar Pesos
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="manifiesto_crear"
                        checked={funciones?.manifiesto_crear}
                        onChange={() => handleChange("manifiesto_crear")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Crear Manifiestos
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
						name="manifiesto_receptar"
                        checked={funciones?.manifiesto_receptar}
                        onChange={() => handleChange("manifiesto_receptar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Receptar Manifiestos
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="manifiesto_buscar"
                        checked={funciones?.manifiesto_buscar}
                        onChange={() => handleChange("manifiesto_buscar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Buscar Manifiestos
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="manifiesto_asignar_cartero"
                        checked={funciones?.manifiesto_asignar_cartero}
                        onChange={() => handleChange("manifiesto_asignar_cartero")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Asignar A Cartero
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="manifiesto_buscar_asignacion"
                        checked={funciones?.manifiesto_buscar_asignacion}
                        onChange={() => handleChange("manifiesto_buscar_asignacion")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Buscar Asignaciones
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="manifiesto_reg_correspondencia"
                        checked={funciones?.manifiesto_reg_correspondencia}
                        onChange={() => handleChange("manifiesto_reg_correspondencia")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Registro de Correspondencia (Dar de Baja)
                      </label>
                    </div>
                  </div>
                </div>
                <div className="col-lg-3 ">
                  <label className="form-label-input">
                    Gestion de Usuarios
                  </label>
                  <div className="mt-3">
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="usuario_crear"
                        checked={funciones?.usuario_crear}
                        onChange={() => handleChange("usuario_crear")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Crear Usuarios
                      </label>
                    </div>
					<div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="usuario_rol_crear"
                        checked={funciones?.usuario_rol_crear}
                        onChange={() => handleChange("usuario_rol_crear")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Crear Roles de Usuarios
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="usuario_buscar"
                        checked={funciones?.usuario_buscar}
                        onChange={() => handleChange("usuario_buscar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Buscar Usuarios
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="usuario_editar"
                        checked={funciones?.usuario_editar}
                        onChange={() => handleChange("usuario_editar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Editar Usuarios
                      </label>
                    </div>
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="usuario_eliminar"
                        checked={funciones?.usuario_eliminar}
                        onChange={() => handleChange("usuario_eliminar")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Eliminar Usuarios
                      </label>
                    </div>
                  </div>
                </div>
                <div className="col-lg-3 ">
                  <label className="form-label-input">
                    Gestion de Plataforma
                  </label>
                  <div className="mt-3">
                    <div className="form-check text-start">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        name="plataforma_asignacion"
                        checked={funciones?.plataforma_asignacion}
                        onChange={() => handleChange("plataforma_asignacion")}
                      />
                      <label
                        className="form-check-label"
                      >
                        Asignar Acciones
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>

        <div className="card card-body">
          <div className="row">
            <button type="button" className="btn-dt col mx-1">
              Cancelar
            </button>
            <button
              type="button"
              className="btn btn-success col mx-1"
              onClickCapture={actualizarAsignacion}
            >
              Guardar
            </button>
          </div>
        </div>

        {/* <div className="card card-body">
					<div className="row">
						<button type="button" className=" btn-dt-del col mx-1 p-2 bt-a">
							Cancelar
						</button>
						<button type="button" className=" btn-dt-details col mx-1 bt-a">
							Anular
						</button> */}
        {/* <button type="button" className="btn-dt col mx-1">Elimina</button>
                    <button type="button" className="btn btn-success col mx-1">Imprimir</button> */}
        {/* </div>
				</div> */}
      </div>
    </div>
  );
}

export default AsignacionAcciones;
