import React, { useEffect } from "react";
import "../crear-guias/crearGuias.css";
import { UserFormCrearGuias } from "../../../../hooks/user_form_crearGuias";
import { useSelector } from "react-redux";
import Dashfooter from "../../../dash-componets/footter/dashfooter";
import EstructuraGuia from "../../../estructura-guia-imprimir/estructura-print";
import { localidadesList } from "../../../../constants/db_provincias";
import ItemListaCreadas from "./item_list_guias_creadas";
import { generateListGuias } from "../../../../helpers/estructura-pdf";
import { listTextGuias } from "../../../../constants/constants";

function CrearGuias() {
  const {
    guias: {
      currentEditGuia,
      fetching,
      guiasAgregadas,
      ListPrintGuias,
      showtoSalve,
    },
  } = useSelector((state) => state);

  const [
    rolUser,
    values,
    printLog,
    setGuiasDb,
    clicPesoVol,
    SaveGuiaStorage,
    setValues,
    handledChange,
    dataProfile,
  ] = UserFormCrearGuias(currentEditGuia);

  useEffect(() => {
    setValues(checkUser(currentEditGuia));
  }, [currentEditGuia]);
  let listaGuiasdoc = generateListGuias(
    guiasAgregadas.length === 0 ? ListPrintGuias : guiasAgregadas
    // listTextGuias
  );
  const checkUser = (dataGuia) => {
    if (rolUser === "Corporativo") {
      console.log("datos del cliente");
      console.log(dataProfile);
      let tempData = {
        ...currentEditGuia,
        remitente: {
          razonSocial: dataProfile?.razonSocial,
          rol: dataProfile?.rol,
          ruc: dataProfile?.ruc,
          direccion: dataProfile?.direccion,
          referencia: dataProfile?.referencia,
          provincia: dataProfile?.provincia,
          canton: dataProfile?.canton,
          parroquia: dataProfile?.parroquia,
          responsable: dataProfile?.responsable,
          telefonoEmpresa: dataProfile?.telefonoEmpresa,
          telefonoResponsable: dataProfile?.telefonoResponsable,
          codigoPostal: dataProfile?.codigoPostal,
          idRemitente: dataProfile?.id,
          email: dataProfile?.email,
        },
      };
      return tempData;
    }
    return dataGuia;
  };

  let blockInputs = rolUser === "Corporativo" ? true : false;
  let counterGuia = 0;
  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4 scroll-section">
      {/* d-print-none */}
      <div className="d-print-none">
        <div className="container-fluid pt-4">
          <h4 className="title-separtor ">Crear Guía</h4>
          <hr />
          <div className="card card-body">
            <div className="  description text-muted text-start   ">
              <p>
                <b>Importante</b>
              </p>
              <ul>
                <li>
                  <p>
                    Se solicita que todo tipo de envíos, sean documentación y/o
                    paquetería, tengan su respectiva <b>guía de remisión</b>, la
                    misma que debe tener todos los campos llenos de forma clara,
                    con
                    <b> datos reales</b> y colocados en una zona visible del
                    envío.
                  </p>
                </li>
                <li>
                  <p>
                    Todos los campos con <b>asterisco (*) </b> son obligatorios
                  </p>
                </li>
              </ul>
            </div>
          </div>
          <div className="form">
            {/* <form onSubmit={handleSubmit}> */}
            <form
              onSubmit={SaveGuiaStorage}
              className="g-3 needs-validation"
              novalidate
            >
              <div className=" d-print-none">
                <h5 className="titleSection-form pt-4">Información de Guia</h5>
                <hr />
                <div className="card p-4 ">
                  <div className="row">
                    <div className="col-lg-6 border-separator">
                      <h5 className="titleSection-form">Remitente</h5>
                      <hr />
                      <div className="mb-3">
                        <label className="form-label-input">
                          {rolUser == "Punto de venta"
                            ? "Cédula / Ruc *"
                            : "Cédula / Ruc"}
                        </label>
                        <input
                          // complete-indo
                          type="text"
                          className={`form-control input-UPER input-UPER ${
                            values?.remitente?.ruc.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          placeholder="Ruc / Cédula / DNI"
                          value={values?.remitente?.ruc}
                          name="remitente ruc"
                          onChange={handledChange}
                          required={rolUser == "Punto de venta" ? true : false}
                          readOnly={blockInputs}
                        />
                      </div>
                      <div className="mb-3">
                        <label
                          className="form-label-input"
                          for="validationTextarea"
                        >
                          Nombres *
                        </label>
                        <input
                          type="text"
                          className={`form-control input-UPER input-UPER ${
                            values?.remitente?.responsable.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          placeholder="Nombres Completos"
                          value={values?.remitente?.responsable}
                          name="remitente responsable"
                          onChange={handledChange}
                          required
                          readOnly={blockInputs}
                        />
                        <div className="invalid-feedback text-start">
                          Ingrese los nombres completos.
                        </div>
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Razón Social</label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.remitente?.razonSocial.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          placeholder="Nombre de la Empresa, Institución o Local."
                          value={values?.remitente?.razonSocial}
                          name="remitente razonSocial"
                          onChange={handledChange}
                          readOnly={blockInputs}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">
                          Teléfono Empresa*
                        </label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.remitente?.telefonoEmpresa.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          onChange={handledChange}
                          placeholder="telefonoEmpresa"
                          value={values?.remitente?.telefonoEmpresa}
                          name="remitente telefonoEmpresa"
                          required
                          readOnly={blockInputs}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Teléfono 2</label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.remitente?.telefonoResponsable.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          onChange={handledChange}
                          placeholder="teléfono Responsable"
                          value={values?.remitente?.telefonoResponsable}
                          name="remitente telefonoResponsable"
                          readOnly={blockInputs}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Provincia *</label>
                        <select
                          className={`form-select ${
                            values?.remitente?.provincia.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          onChange={handledChange}
                          value={values?.remitente?.provincia}
                          name="remitente provincia"
                          required
                          disabled={blockInputs}
                        >
                          <option key={`#1_remitente`} value="">
                            Seleccionar la Provincia
                          </option>
                          {localidadesList.map((e) => (
                            <option
                              key={`${e.provincia}_remitente`}
                              value={e.provincia}
                            >
                              {e.provincia}
                            </option>
                          ))}
                        </select>
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Cantón *</label>
                        <select
                          className={`form-select ${
                            values?.remitente?.canton.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          name="remitente canton"
                          onChange={handledChange}
                          value={values?.remitente?.canton}
                          required
                          disabled={blockInputs}
                        >
                          <option key={`#2_remitente`} value="">
                            Seleccionar el Cantón
                          </option>

                          {localidadesList
                            .filter((item) => {
                              if (
                                item.provincia === values?.remitente?.provincia
                              ) {
                                return item;
                              }
                            })[0]
                            ?.cantones.map((e) => {
                              return (
                                <option
                                  key={`${e.canton}_canton_remitente`}
                                  value={e.canton}
                                >
                                  {e.canton}
                                </option>
                              );
                            })}
                        </select>
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Parroquia *</label>
                        <select
                          className={`form-select ${
                            values?.remitente?.parroquia.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          name="remitente parroquia"
                          onChange={handledChange}
                          value={values?.remitente?.parroquia}
                          required
                          disabled={blockInputs}
                        >
                          <option key={`#2_remitente`} value="">
                            Seleccionar la Parroquia
                          </option>

                          {localidadesList
                            .filter((item) => {
                              if (
                                item.provincia === values?.remitente?.provincia
                              ) {
                                return item;
                              }
                            })[0]
                            ?.cantones.filter((item) => {
                              if (item.canton === values?.remitente?.canton) {
                                return item;
                              }
                            })[0]
                            ?.parroquias.map((e) => {
                              return (
                                <option
                                  key={`${e}_parroquia_remitente`}
                                  value={e}
                                >
                                  {e}
                                </option>
                              );
                            })}
                        </select>
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Dirección *</label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.remitente?.direccion.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          onChange={handledChange}
                          placeholder="Calle principal, e intersección (maximo 70 caracateres)"
                          maxlength="70"
                          value={values?.remitente?.direccion}
                          name="remitente direccion"
                          required
                          readOnly={blockInputs}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Referencia *</label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.remitente?.referencia?.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          onChange={handledChange}
                          placeholder="Adjunte número de casa o descripción breve del destino"
                          maxLength="90"
                          value={values?.remitente?.referencia}
                          name="remitente referencia"
                          readOnly={blockInputs}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">
                          Código Postal
                        </label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.remitente?.codigoPostal?.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          onChange={handledChange}
                          placeholder="Num. Código postal"
                          name="remitente codigoPostal"
                          value={values?.remitente?.codigoPostal}
                          required
                          readOnly={blockInputs}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Email</label>
                        <input
                          type="email"
                          className={`form-control input-UPER ${
                            values?.remitente?.email?.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          onChange={handledChange}
                          placeholder="example@example.com"
                          name="remitente email"
                          value={values?.remitente?.email}
                          readOnly={blockInputs}
                        />
                      </div>
                    </div>
                    <div className="col-lg-6 ">
                      <h5 className="titleSection-form">Destinatario</h5>
                      <hr />
                      <div className="mb-3">
                        <label className="form-label-input">Cédula / Ruc</label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.destinatario?.ruc.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          placeholder="110XXXXXXXXX"
                          value={values?.destinatario?.ruc}
                          name="destinatario ruc"
                          onChange={handledChange}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Nombre *</label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.destinatario?.nombres?.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          placeholder="Nombres Completos"
                          value={values?.destinatario?.nombres}
                          name="destinatario nombres"
                          onChange={handledChange}
                          required
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Razón Social</label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.destinatario?.razonSocial?.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          placeholder="Nombre de la Empresa, Institución o Local."
                          value={values?.destinatario?.razonSocial}
                          name="destinatario razonSocial"
                          onChange={handledChange}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">
                          Télefono Principal *
                        </label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.destinatario?.telefono1.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          placeholder="0978888888"
                          onChange={handledChange}
                          name="destinatario telefono1"
                          value={values?.destinatario?.telefono1}
                          required
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">
                          Télefono Secundario
                        </label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.destinatario?.telefono2.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          onChange={handledChange}
                          placeholder="Télefono 1"
                          name="destinatario telefono2"
                          value={values?.destinatario?.telefono2}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Provincia *</label>
                        <select
                          className={`form-select input-UPER ${
                            values?.destinatario?.provincia.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          onChange={handledChange}
                          value={values?.destinatario?.provincia}
                          name="destinatario provincia"
                          required
                        >
                          <option key={`#1_destinatario`} value="">
                            Seleccionar la Provincia
                          </option>
                          {localidadesList.map((e) => (
                            <option
                              key={`${e.provincia}_destinatario`}
                              value={e.provincia}
                            >
                              {e.provincia}
                            </option>
                          ))}
                        </select>
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Cantón *</label>
                        <select
                          className={`form-select input-UPER ${
                            values?.destinatario?.canton.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          name="destinatario canton"
                          onChange={handledChange}
                          value={values?.destinatario?.canton}
                          required
                        >
                          <option key={`#2_destinatario`} value="">
                            Seleccionar el Cantón
                          </option>

                          {localidadesList
                            .filter((item) => {
                              if (
                                item.provincia ===
                                values?.destinatario?.provincia
                              ) {
                                return item;
                              }
                            })[0]
                            ?.cantones.map((e) => {
                              return (
                                <option
                                  key={`${e.canton}_canton_destinatario`}
                                  value={e.canton}
                                >
                                  {e.canton}
                                </option>
                              );
                            })}
                        </select>
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Parroquia</label>
                        <select
                          className={`form-select input-UPER ${
                            values?.destinatario?.parroquia.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          name="destinatario parroquia"
                          onChange={handledChange}
                          value={values?.destinatario?.parroquia}
                        >
                          <option key={`#2_destinatario`} value="">
                            Seleccionar la Parroquia
                          </option>

                          {localidadesList
                            .filter((item) => {
                              if (
                                item.provincia ===
                                values?.destinatario?.provincia
                              ) {
                                return item;
                              }
                            })[0]
                            ?.cantones.filter((item) => {
                              if (
                                item.canton === values?.destinatario?.canton
                              ) {
                                return item;
                              }
                            })[0]
                            ?.parroquias.map((e) => {
                              return (
                                <option
                                  key={`${e}_parroquia_destinatario`}
                                  value={e}
                                >
                                  {e}
                                </option>
                              );
                            })}
                        </select>
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Dirección *</label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.destinatario?.direccion.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          onChange={handledChange}
                          placeholder="Calle principal, e intersección"
                          value={values?.destinatario?.direccion}
                          name="destinatario direccion"
                          required
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Referencia *</label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.destinatario?.referencia.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          onChange={handledChange}
                          placeholder="Adjunte número de casa o descripción breve del destino"
                          value={values?.destinatario?.referencia}
                          name="destinatario referencia"
                          required
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">
                          Código Postal
                        </label>
                        <input
                          type="text"
                          className={`form-control input-UPER ${
                            values?.destinatario?.codigoPostal.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          onChange={handledChange}
                          placeholder="11XXXXXX"
                          name="destinatario codigoPostal"
                          value={values?.destinatario?.codigoPostal}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">Email</label>
                        <input
                          type="email"
                          className={`form-control input-UPER ${
                            values?.destinatario?.email.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          onChange={handledChange}
                          placeholder="example@example.com"
                          name="destinatario email"
                          value={values?.destinatario?.email}
                          required
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <h5 className="titleSection-form">Informacion del Envío</h5>
                <hr />
                <div className="card">
                  <div className="row p-4">
                    <div className="col-lg-6  border-separator  ">
                      <div className="mb-3">
                        <label className="form-label-input">
                          Tipo Producto
                        </label>
                        <select
                          className={`form-select input-UPER ${
                            values?.tipo?.length >= 1 ? "complete-indo" : ""
                          }`}
                          value={values?.tipo}
                          name="tipo"
                          onChange={handledChange}
                          required
                        >
                          <option value="">Seleccione Producto</option>
                          <option value="documento">Documento</option>
                          <option value="paquete">Paquete</option>
                        </select>
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">
                          Número de Piezas
                        </label>
                        <input
                          className={`form-select input-UPER ${
                            values?.numPiezas?.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          type="text"
                          oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*?)\..*/g, '$1');"
                          placeholder="(0) Solo números enteros"
                          maxlength="3"
                          minlength="1"
                          name="numPiezas"
                          value={values?.numPiezas}
                          onChange={handledChange}
                          readOnly={
                            values?.tipo === "documento" ||
                            values?.tipo === "" ||
                            values.tipo === undefined
                              ? true
                              : false
                          }
                        />
                      </div>
                      <div className="mb-3 text-end">
                        <label className="form-label-input">Contenido</label>
                        <textarea
                          className={`form-control input-UPER ${
                            values?.contenido?.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          rows={5}
                          placeholder="Describe el contenido del envío, maximo 40 caracteres..."
                          onChange={handledChange}
                          value={values?.contenido}
                          maxlength="65"
                          minlength="5"
                          name="contenido"
                          required
                        />
                        {values?.contenido.length === 65 ? (
                          <span className="mr-2 mt-2 text-muted sized-max">
                            Maximo de Caracteres
                          </span>
                        ) : (
                          <></>
                        )}
                        <span class="badge bg-secondary">
                          {values?.contenido.length}/65
                        </span>
                      </div>
                    </div>

                    <div className="col-lg-6">
                      <div className="mb-3">
                        <label className="form-label-input">Peso</label>
                        <input
                          className={`form-control input-UPER ${
                            values?.peso?.pesototal?.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          type="text"
                          oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*?)\..*/g, '$1');"
                          placeholder="0.00 Kg"
                          name="peso pesototal"
                          min={0}
                          onChange={handledChange}
                          value={values?.peso?.pesototal}
                          readOnly={values?.pesoVol || rolUser === "Corporativo" || values?.tipo === "documento"}
                        />
                      </div>
                      {values?.tipo === "documento" ||
                      values?.tipo === "" ||
                      rolUser == "Corporativo" ? (
                        <></>
                      ) : (
                        <div className="mb-3 text-start">
                          <a
                            className="nav-link"
                            href="#pesosvlcrear"
                            data-bs-toggle="collapse"
                            role="button"
                            aria-expanded="false"
                            aria-controls="pesosvlcrear"
                            onClick={clicPesoVol}
                          >
                            Agregar Peso Volumétrico
                          </a>
                        </div>
                      )}
                      <div
                        className="collapse w-100 align-items-start"
                        id="pesosvlcrear"
                      >
                        <div className="list-group-flush">
                          <div className="mb-3">
                            <label className="form-label-input">
                              Largo <span className="text-muted">(cm)</span>
                            </label>
                            <input
                              className={`form-control input-UPER ${
                                values?.peso?.largo?.length >= 1
                                  ? "complete-indo"
                                  : ""
                              }`}
                              type="number"
                              placeholder="0.00 Kg"
                              name="peso largo"
                              onChange={handledChange}
                              value={values?.peso?.largo}
                              min={0}
                            />
                          </div>
                          <div className="mb-3">
                            <label className="form-label-input">
                              Ancho <span className="text-muted">(cm)</span>
                            </label>
                            <input
                              className={`form-control input-UPER ${
                                values?.peso?.ancho?.length >= 1
                                  ? "complete-indo"
                                  : ""
                              }`}
                              type="number"
                              placeholder="0.00 Kg"
                              name="peso ancho"
                              onChange={handledChange}
                              value={values?.peso?.ancho}
                              min={0}
                            />
                          </div>
                          <div className="mb-3">
                            <label className="form-label-input">
                              Alto <span className="text-muted">(cm)</span>
                            </label>
                            <input
                               className={`form-control input-UPER ${
                                values?.peso?.alto?.length >= 1
                                  ? "complete-indo"
                                  : ""
                              }`}
                              type="number"
                              placeholder="0.00 Kg"
                              name="peso alto"
                              onChange={handledChange}
                              value={values?.peso?.alto}
                              min={0}
                            />
                          </div>
                        </div>
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">
                          Valor Declarado
                        </label>
                        <input
                          type="number"
                          className={`form-control input-UPER ${
                            values?.v_declarado?.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          placeholder="$ 0.00"
                          name="v_declarado"
                          value={values?.v_declarado}
                          onChange={handledChange}
                        />
                      </div>
                      <div className="mb-3">
                        <label className="form-label-input">
                          Valor Asegurado
                        </label>
                        <input
                          name="v_asegurado"
                          value={values?.v_asegurado}
                          type="text"
                          className={`form-control input-UPER ${
                            values?.v_asegurado?.length >= 1
                              ? "complete-indo"
                              : ""
                          }`}
                          placeholder="$ 0.00"
                          readOnly
                        />
                      </div>
                    </div>
                  </div>
                  <button
                    type="submit"
                    className={
                      currentEditGuia?.destinatario?.nombres === ""
                        ? "btn-solid-prymary w-80 p-2 m-2"
                        : "btn-solid-secondary w-80 p-2 m-2"
                    }
                  >
                    {currentEditGuia?.destinatario?.nombres === ""
                      ? "Crear Guia"
                      : "Editar Guia"}
                  </button>
                </div>
                <h5 className="titleSection-form">Lista de Guias Creadas</h5>
                <hr />
                <div className="card p-2">
                  <div className="table-cm table-responsive">
                    <table className="table nowrap ">
                      <thead>
                        <tr className="header-table">
                          <th
                            className=" text-truncate"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Remitente"
                          >
                            Remitente
                          </th>
                          <th
                            className=" text-truncate"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Destinatario"
                          >
                            Destinatario
                          </th>
                          <th
                            className=" text-truncate"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Fecha"
                          >
                            Fecha
                          </th>
                          <th
                            className=" text-truncate"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Origen"
                          >
                            Origen
                          </th>
                          <th
                            className=" text-truncate"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Destino"
                          >
                            Destino
                          </th>
                          <th
                            className=" text-truncate"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Editar"
                          >
                            Editar
                          </th>
                          <th
                            className=" text-truncate"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Eliminar"
                          >
                            Eliminar
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        {guiasAgregadas?.map((guia) => {
                          return <ItemListaCreadas guia={guia} />;
                        })}
                      </tbody>
                    </table>
                    {guiasAgregadas.length >= 1 ? (
                      <></>
                    ) : (
                      <div className="text-center">
                        <p className="text-muted">No se ha agregado guías</p>
                      </div>
                    )}
                  </div>
                  <hr />
                  <div className="row">
                    <div className="col-sm-12 col-md-12 ">
                      <button
                        type="button"
                        className="btn btn-primary w-100 p-2"
                        onClick={setGuiasDb}
                        disabled={guiasAgregadas.length === 0 ? true : false}
                      >
                        {
                          //TODO mostrar alarta para crear guias o guardarlas
                          fetching ? "Creando Guías..." : "Generar Guías"
                        }
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
        <div></div>
        <button onClick={()=>window.print()}>SDA SAD SAD SAD SA</button>
      </div>
      <Dashfooter />
      <div id="guias-export" className="guias-print">
      {/* <div id="guias-export" className="guias-print d-none d-print-block"> */}
        {listaGuiasdoc.map((guiaPrint) => {
          counterGuia = counterGuia + 1;
          return (
            <div className="section-print-guias">
              <EstructuraGuia guia={guiaPrint} />
              <EstructuraGuia guia={guiaPrint} />
              <EstructuraGuia
                guia={guiaPrint}
                showLine={false}
                showSpace={listaGuiasdoc.length === counterGuia ? false : true}
              />
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default CrearGuias;
