import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useToasts } from "react-toast-notifications";
import { truncate } from "../helpers/helper";
import {
  currentGuiaDetail,
  findGuiaPesos,
  currentEditGuia,
  limpiarListaGuiasPesos,
  currentGuiaDetailPesos,
  actualizarPesoGuias,
  addGuiaConPesoActualizado,
  updateGuiaPesoEdit,
  deletefromPesosActualizados,
  setEditGuia,
} from "../redux/actions/guias_actions";

function ActualizarPesosHook() {
  const dispatch = useDispatch();
  const { addToast } = useToasts();
  const initValues = {
    idGuia: "",
    pesoVol: false,
    onEdit: false,
  };
  const {
    guias: {
      ListGuiasParaPeso,
      fetching,
      msgError,
      currentDetailGuiaPesos,
      ListGuiasPesoActualizado,
    },
    auth: { dataProfile },
  } = useSelector((state) => state);

  const rolUser =
    dataProfile?.rol === "admin"
      ? "Administrador"
      : dataProfile?.rol === "puntoVenta"
      ? "Punto de venta"
      : dataProfile?.rol === "sucursal"
      ? "Sucursal"
      : "Corporativo";

  const [values, setvalues] = useState({
    pesoVol: false,
    edit: false,
  });

  const [numGuia, setNumGuia] = useState("");

  const errorSms = (sms) => toastSms(`${sms}`, "error");
  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };
  const handleSearchGuia = (e) => {
    e.preventDefault();
    setNumGuia(e.target.value.trim());
    // const successSms = () => toastSms(`Guia Agregada`, "success");
    // if (e.target.value.trim()) {
    //   let res = e.target.value.replace(/ /g, "");
    //   if (ListGuiasParaPeso.idGuia === `${res}`) {
    //     toastSms(`Esta guia ya fue seleccionada`, "info");
    //   } else {
    //     dispatch(findGuiaPesos(res, errorSms, successSms));
    //   }
    // } else {
    //   toastSms("debes ingresar un valor", "info");
    // }
  };
  const handleOnBlur = (e) => {
    const successSms = () => toastSms(`Guia Agregada`, "success");
    if (e.target.value.trim()) {
      let res = e.target.value.replace(/ /g, "");
      if (ListGuiasParaPeso.idGuia === `${res}`) {
        toastSms(`Esta guia ya fue seleccionada`, "info");
      } else {
        dispatch(findGuiaPesos(res, errorSms, successSms));
      }
    } else {
      toastSms("debes ingresar un valor", "info");
      document.getElementById("searchGuia").focus();
    }
  };

  const handleChangeValue = (e) => {
    e.preventDefault();
    setvalues({
      ...values,
      [e.target.name]: e.target.value,
    });
    if (values?.pesoVol) {
      let total =
        (parseFloat(values?.largo ?? 0.0) *
          parseFloat(values?.ancho ?? 0.0) *
          parseFloat(values?.alto ?? 0.0)) /
        6000;
      setvalues({
        ...values,
        pesototal: truncate(total, 4),
        [e.target.name]: e.target.value,
      });
    }
  };
  const ActivePesoVol = () => {
    if (values?.pesoVol === false) {
      setvalues({
        ...values,
        pesoVol: !values?.pesoVol,
        pesototal: "",
        largo: "",
        ancho: "",
        alto: "",
      });
    } else {
      setvalues({
        ...values,
        pesoVol: !values?.pesoVol,
      });
    }
  };
  const agregarAListaActualizados = (e) => {
    e.preventDefault();
    if (values?.pesototal === "") {
      toastSms("Debes ingresar un peso", "error");
      document.getElementById("input-peso").focus();
    } else {
      let tempGuia = {
        ...ListGuiasParaPeso,
        peso: {
          largo: values?.largo ?? "",
          alto: values?.alto ?? "",
          ancho: values?.ancho ?? "",
          pesototal: values?.pesototal ?? "",
        },
        updatePeso: {
          fechaPesoActualizado: Date.now().toString(),
          rol: rolUser,
          actualizadoPor: dataProfile?.email,
        },
        estado: "peso actualizado",
      };
      dispatch(addGuiaConPesoActualizado(tempGuia));
      toastSms("Peso Actualizado", "success");
      setTimeout(() => {
        document.getElementById("searchGuia").focus();
      }, 450);
    }
  };
  const editGuiaPeso = (guia) => {
    setvalues({
      ...values,
      pesoVol: true,
      edit: true,
      pesototal: guia?.peso?.pesototal,
      largo: guia?.peso?.largo,
      ancho: guia?.peso?.ancho,
      alto: guia?.peso?.alto,
      editGuia: guia,
    });
  };

  const updateGuiaonList = (e) => {
    e.preventDefault();
    let temp = {
      ...values.editGuia,
      peso: {
        largo: values?.largo ?? "",
        alto: values?.alto ?? "",
        ancho: values?.ancho ?? "",
        pesototal: values?.pesototal ?? "",
      },
    };

    dispatch(updateGuiaPesoEdit(temp, ListGuiasPesoActualizado));

    setvalues({
      ...values,
      pesoVol: false,
      edit: false,
      pesototal: "",
      largo: "",
      ancho: "",
      alto: "",
      editGuia: {},
    });
    document.getElementById("searchGuia").focus();
  };
  const eliminardeListaActualizados = (guia) => {
    let listtemp = ListGuiasPesoActualizado.filter(
      (guiaitem) => guiaitem.idGuia !== guia.idGuia
    );

    dispatch(deletefromPesosActualizados(listtemp));
    toastSms("Item eliminado de la lista", "info");
  };

  const updateGuiasFirebase = () => {
    const errorSms = () => toastSms(`${msgError}`, "error");
    const successSms = () =>
      toastSms("Registro Guardado", "success");
    dispatch(
      actualizarPesoGuias(ListGuiasPesoActualizado, successSms, errorSms)
    );
    setvalues({});
  };

  return [
    ListGuiasParaPeso,
    fetching,
    currentDetailGuiaPesos,
    ListGuiasPesoActualizado,
    values,
    agregarAListaActualizados,
    ActivePesoVol,
    handleChangeValue,
    handleSearchGuia,
    editGuiaPeso,
    updateGuiaonList,
    eliminardeListaActualizados,
    updateGuiasFirebase,
    handleOnBlur,
  ];
}

export default ActualizarPesosHook;
