import { useCallback, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useToasts } from "react-toast-notifications";
import { actualizarRol, consultarRoles } from "../redux/actions/roles_actions";
function AsignacionHook() {
  const dispatch = useDispatch();
  const { addToast } = useToasts();

  const [user, setUser] = useState("");
  const [funciones, setFunciones] = useState({});
  const [id, setId] = useState("");
  const [val, setVal] = useState(false);

  const buscar = useCallback(() => {
    dispatch(consultarRoles());
  }, [dispatch]);

  const {
    roles: { listRoles, fetching },
  } = useSelector((state) => state);

  useEffect(() => {
    if (listRoles === null && !fetching) {
      buscar();
    }
  }, [listRoles, fetching, buscar]);

  //mensajes
  const msgOk = () => {
    return addToast("Funciones actualizadas con éxito", {
      appearance: "success",
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };
  const msgError = (title) => {
    return addToast(title, {
      appearance: "error",
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };

  const handleChange = (e) => {
 
    setVal(!val);
    const tempMap = funciones;
    console.log(tempMap);
    tempMap[e] = !funciones[e];
    setFunciones(tempMap);
  };

  const changeValue = ({ target }) => {
    setUser(target.value);
    listRoles?.forEach((rol) => {
      if (rol.rol === target.value) {
        setFunciones(rol.funciones);
        setId(rol.id);
      }
    });
  };

  const actualizarAsignacion = () => {
    // if (user === "") {
    // 	msgError("Debes de seleccionar un rol")
    // } else {
    dispatch(actualizarRol(id, funciones, msgError, msgOk));
    // }
  };
  const switchRol = (rol) => {
    switch (rol) {
      case "admin":
        return "Administrador";
      case "puntoVenta":
        return "Punto de Venta";
      case "sucursal":
        return "Sucursal";
      case "sucursal":
        return "Sucursal";
      case "cartero" :
        return "Agente de Entrega / Cartero";  
      default:
        return "Cliente Corporativo";
    }
  };
  return [
    user,
    fetching,
    funciones,
    listRoles,
    switchRol,
    actualizarAsignacion,
    changeValue,
    handleChange,
    buscar,
  ];
}

export default AsignacionHook;
