import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useToasts } from "react-toast-notifications";
import {
  actualizarManifiestosyGuias,
  comprobarItems,
  findManifiesto,
  setSearchIdGuiaManifiesto,
  updateGuiaStatus,
} from "../redux/actions/manifiestos_actions";

function RecepcionManifiestoHook() {
  const {
    manifiestos: {
      msgError,
      fetching,
      currentManifiesto,
      searchIdReseptarGuia,
      numRecibidos,
      numMalEncaminados,
      numPendientes,
    },
  } = useSelector((state) => state);

  const dispatch = useDispatch();

  const { addToast } = useToasts();
  const [values, setvalues] = useState({
    idManifiesto: "",
    idGuia: "",
    itemResividos: 0,
    malEncaminados: 0,
    pendientes: 0,
  });

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };

  //se ecuta al finalizar de escribir
  const handlerSearchText = (e) => {
    e.preventDefault();
    setvalues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const setIdGuiaSearch = (e) => {
    console.log(e.target.value);
    e.preventDefault();
    dispatch(setSearchIdGuiaManifiesto(e.target.value));
    if (e.target.value.trim()) {
      let res = e.target.value.replace(/ /g, "");
      actualizarEstadoGuia(res);
    } else {
      toastSms("No hay un Num. de guia a buscar", "error");
    }
  };
  const errorManifiesto = (error) => {
    return toastSms(error, "error");
  };
  const succesManifiesto = () => {
    return toastSms("Manifiesto encontrada", "success");
  };

  const searchManifiesto = (e) => {
    // console.log("entro a la funcion");
    //! despues actualizar
    e.preventDefault();
    setvalues({
      ...values,
      [e.target.name]: e.target.value,
    });
    console.log(e.target.value);
    if (e.target.value.trim()) {
      let res = e.target.value.replace(/ /g, "");
      dispatch(findManifiesto(res, succesManifiesto, errorManifiesto));
    } else {
      toastSms("Debes ingresar un Valor", "error");
    }
  };

  const actualizarEstadoGuia = (idGuia) => {
    let guia = currentManifiesto?.listGuias?.filter(
      (item) => item.idGuia === idGuia
    );
    let tempItem = {
      idGuia: guia[0]?.idGuia,
      estado: "receptado",
      peso: guia[0]?.peso,
    };
    dispatch(
      updateGuiaStatus(currentManifiesto?.listGuias, tempItem?.idGuia, tempItem)
    );
    toastSms("Guia Actualizada", "success");
  };
  const actualizarManifiestoGuiaDB = () => {
    const susccess = () =>
      toastSms("Manifiesto actualizacon con exito", "success");
    const error = (error) => toastSms(`Error al actualizar ${error}`, "error");
    const printManifiesto = () => window.print();
    dispatch(actualizarManifiestosyGuias(currentManifiesto, susccess, error, printManifiesto));
  };
  return [
    values,
    fetching,
    currentManifiesto,
    numRecibidos,
    numMalEncaminados,
    searchIdReseptarGuia,
    numPendientes,
    actualizarManifiestoGuiaDB,
    handlerSearchText,
    searchManifiesto,
    actualizarEstadoGuia,
    setIdGuiaSearch,
  ];
}

export default RecepcionManifiestoHook;
