//buscar guias por id para agregar al la lista de manifiestos
export const findGuiaManifiesto = (idGuia, showError, comprobarGuia) => {
  return async (dispatch) => {
    dispatch({
      type: GET_GUIA_MANIFIESTO,
    });
    try {
      let resp = await db
        .collection("guias")
        .doc(`${idGuia}`)
        .get()
        .then((doc) => {
          return doc.data();
        });
      // para agregregarse la guia debe de estar en peso actualizado
      // console.log(resp.estado);
      if (resp === undefined) {
        showError("No existe guia con este número de referencia");
        dispatch({
          type: GET_GUIA_MANIFIESTO_ERROR,
          payload: "",
        });
      } else if (resp.estado !== "peso actualizado") {
        showError("Aun no se actualizado el peso de esta guía");
        dispatch({
          type: GET_GUIA_MANIFIESTO_ERROR,
          payload: "",
        });
      } else if (resp.estado === "peso actualizado") {
        await dispatch({
          type: GET_GUIA_MANIFIESTO_SUCCESS,
          payload: {},
        });
        comprobarGuia(resp?.destinatario?.provincia, resp);
      } else {
        showError("Error al agregar guia a manifiesto");
      }
    } catch (error) {
      dispatch({
        type: GET_GUIA_MANIFIESTO_ERROR,
        payload: "Error al buscar guías",
      });
      showError(`Error al agregar guia a manifiesto ${error.toString()}`);
    }
  };
};

