export const crearManifiesto = (
  guias,
  mostrarAlertaOk,
  mostrarAlertaError,
  printGuias
) => {
  return async (dispatch) => {
    dispatch({
      type: CREATE_MANIFIESTO_DB,
    });
    try {
      guias.listGuias.map(async (guia) => {
        await db
          .collection("guias")
          .doc(`${guia.idGuia}`)
          .update({
            estado: "agregado manifiesto",
            addManifiesto: {
              fecha: `${guias.fechaCreacion}`,
              creadoPor: `${guias.creadoPor}`,
              idManifiesto: `${guias.idManifiesto}`,
            },
          });
      });
      dispatch({
        type: CLEAN_MANIFIESTO,
        payload: guias,
      });
      await db
        .collection("manifiestos")
        .doc(`${guias.idManifiesto}`)
        .set(guias)
        .then((value) => {
          printGuias();
        });

      dispatch({
        type: CREATE_MANIFIESTO_DB_SUCCESS,
      });
      mostrarAlertaOk("Manifiesto Creado");
    } catch (error) {
      dispatch({
        type: CREATE_MANIFIESTO_DB_ERROR,
      });
      mostrarAlertaError(`error al crear manifiesto ${error}`);
    }
  };
};
