export const actualizarManifiestosyGuias = (
  manifiesto,
  successsms,
  errorsms,
  print
) => {
  return async (dispatch) => {
    dispatch({
      type: UPDATE_MANIFIESTO_DB,
    });
    try {
      let itemlist = manifiesto?.listGuias?.map((itemGuia) => {
        if (itemGuia.estado === "creado") {
          itemGuia.estado = "pendiente";
          return itemGuia;
        } else {
          return itemGuia;
        }
      });
      console.log("lista antes de enviar al db");
      console.log(itemlist);
      //todo guar dar informacion de recempcion de guia por que usuario
      itemlist?.map(async (guia) => {
        await db.collection("guias").doc(`${guia.idGuia}`).update({
          estado: guia.estado,
          fechaReceptado: Date.now().toString(),
        });
        await db
          .collection("manifiestos")
          .doc(`${manifiesto.idManifiesto}`)
          .update({
            listGuias: itemlist,
            fechaReceptado: Date.now().toString(),
            estado: "receptado",
          });
      });
      successsms();
      setTimeout(() => {
        print();
      }, 1500);
      dispatch({
        type: UPDATE_MANIFIESTO_DB_SUCCESS,
      });
    } catch (error) {
      dispatch({
        type: UPDATE_MANIFIESTO_DB_ERROR,
        msgError: error,
      });
      errorsms(`error al receptar manifiesto ${error}`);
    }
  };
};

