export const fingGuiaAsignacion = (idGuia, showError, msgSuccess) => {
  return async (dispatch) => {
    dispatch({
      type: GET_GUIA_ASIGNACION_DB,
    });
    try {
      let resp = await db
        .collection("guias")
        .doc(`${idGuia}`)
        .get()
        .then((doc) => {
          return doc.data();
        });
      if (resp === undefined) {
        showError("No existe guia con este número de referencia");
        dispatch({
          type: GET_GUIA_ASIGNACION_DB_ERROR,
          payload: "",
        });
      } else if (resp.estado === "asignado a cartero") {
        showError("Guia ya Asignada a un Cartero");
        dispatch({
          type: GET_GUIA_ASIGNACION_DB_ERROR,
          payload: "",
        });
      } else {
        await dispatch({
          type: GET_GUIA_ASIGNACION_DB_SUCCESS,
          payload: resp,
        });
        msgSuccess();
      }
    } catch (error) {
      dispatch({
        type: GET_GUIA_ASIGNACION_DB_ERROR,
      });
      showError(`Error al consultar guia: ${error}`);
    }
  };
};

