export const setAsignacionCorrespondencia = (
  asignacion,
  msgError,
  msgSuccess,
  print
) => {
  return async (dispatch) => {
    dispatch({
      type: SET_ASIGNACION_DB,
    });
    try {
      await Promise.all(
        asignacion?.guiasAsignadas.map(async (guia) => {
          await db
            .collection("guias")
            .doc(`${guia?.idGuia}`)
            .update({
              estado: "asignado a cartero",
              asignada: {
                fechaAsignado: asignacion?.fechaCreacion,
                asignadoPor: asignacion?.creadoPor,
                idAsignacion: asignacion?.idAsignacion,
              },
            });
        }),
        await db
          .collection("asignaciones")
          .doc(`${asignacion?.idAsignacion}`)
          .set(asignacion)
      );

      dispatch({
        type: SET_ASIGNACION_DB_SUCCESS,
        payload: asignacion,
      });
      setTimeout(() => {
        print();
      }, 300);
      setTimeout(() => {
        msgSuccess();
      }, 700);
    } catch (error) {
      dispatch({
        type: SET_ASIGNACION_DB_ERROR,
        errorSms: error,
      });
      msgError(`Error Asignación: ${error}`);
    }
  };
};
