import React from "react";
import { DebounceInput } from "react-debounce-input";
import { ParseDate } from "../../../../helpers/helper";
import RegistroCorrespondenciahook from "../../../../hooks/registro_correspondencia_hook";
import Spinner from "../../../spinner/spinner";
import "../asignacion_correspondencia/asignacion_correspondencia.css";
import Dashfooter from "../../../dash-componets/footter/dashfooter";
import { generateListGuias } from "../../../../helpers/estructura-pdf";
import { listTextGuias } from "../../../../constants/constants";
import EstructuraGuia from "../../../estructura-guia-imprimir/estructura-print";

function RegistroCorrespondencia() {
  const [
    msgError,
    fetching,
    listGuiasRegistro,
    currentGuiaRegistro,
    values,
    actualizarGuiasDb,
    addGuiaUpdate,
    handlechangeTate,
    searchGuia,
    deleteGuia,
  ] = RegistroCorrespondenciahook();
  // let listaGuiasdoc = generateListGuias(
  // );
  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4 scroll-section">
      <div className="container-fluid pt-4">
        <h4 className="title-separtor">Registro de Corespondencia</h4>
        <hr />
        <div className="card card-body">
          <div className="row mb-4">
            <div className="col-sm-12 col-md-6">
              <form>
                <div className="input-group">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Buscar Guia
                  </label>
                  <DebounceInput
                    minLength={5}
                    debounceTimeout={500}
                    type="text"
                    className="form-control dropdown-toggle"
                    placeholder="Num. de guíaa..."
                    name="idGuia"
                    // value={values?.idManifiesto}
                    onChange={searchGuia}
                  />
                  <span className="mdi mdi-magnify search-icon" />
                  {fetching ? (
                    <div className="container-spinner-buscar">
                      <Spinner />
                    </div>
                  ) : (
                    <button className="btn-solid-secondary px-4 " type="button">
                      Buscar
                    </button>
                  )}
                </div>
              </form>
            </div>
          </div>

          <div className="row">
            <div className="col-sm-12 col-md-6">
              <h5 className="titleSection-form">Dar de Baja</h5>
            </div>
            <div className="col-sm-12 col-md-6 text-end">
              {currentGuiaRegistro?.idGuia === undefined ? (
                <></>
              ) : (
                <h5 className="titleSection-form text-end">
                  Guia Actual: <b>{currentGuiaRegistro?.idGuia}</b>
                </h5>
              )}
            </div>
          </div>
          <hr />
          <form onSubmit={addGuiaUpdate}>
            <div className="row ">
              <div className="col-sm-12 col-md-6">
                <div className="mb-3">
                  <label className="form-label-input">Recibido por</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Nombres/Apellidos"
                    name="resibido"
                    onChange={handlechangeTate}
                    value={values?.resibido}
                    readOnly={currentGuiaRegistro?.idGuia ? false : true}
                    required
                  />
                </div>
              </div>
              <div className="col-sm-12 col-md-6">
                <div className="input-group">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Num. Cédula/DNI
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Num. ruc/dni..."
                    name="ruc"
                    onChange={handlechangeTate}
                    value={values?.ruc}
                    readOnly={currentGuiaRegistro?.idGuia ? false : true}
                    required
                  />
                </div>
              </div>
            </div>
            <div className="row ">
              <div className="col-sm-12 col-md-6 mb-3">
                <label htmlFor="simpleinput" className="form-label-input">
                  Fecha
                </label>
                <input
                  className="form-control"
                  id="example-date"
                  type="date"
                  name="date"
                  onChange={handlechangeTate}
                  value={values?.date}
                  readOnly={currentGuiaRegistro?.idGuia ? false : true}
                  required
                />
              </div>

              <div className="col-sm-12 col-md-6 mb-3">
                <label htmlFor="simpleinput" className="form-label-input">
                  Hora
                </label>
                <input
                  className="form-control"
                  id="example-time"
                  type="time"
                  name="hora"
                  onChange={handlechangeTate}
                  value={values?.hora}
                  readOnly={currentGuiaRegistro?.idGuia ? false : true}
                  required
                />
              </div>
            </div>
            <div className="row ">
              <div className="col-sm-12 col-md-6">
                <div className="mb-3">
                  <label className="form-label-input">Novedad</label>
                  <select
                    className="form-select overflow-auto"
                    name="novedad"
                    onChange={handlechangeTate}
                    value={values?.novedad}
                    disabled={currentGuiaRegistro?.idGuia ? false : true}
                    required
                  >
                    <option>NADIE EN CASA</option>
                    <option>CAMBIO DE DIRECCIÓN</option>
                    <option>DIRECCIÓN INCORRECTA</option>
                    <option>RECHAZADO</option>
                    <option>DESCONOCIDO</option>
                    <option>VUELTA A GESTIÓN</option>
                    <option>FUERZA MAYOR</option>
                  </select>
                </div>
              </div>
              <div className="col-sm-12 col-md-6 mb-3">
                <label htmlFor="simpleinput" className="form-label-input">
                  Descripcion Novedad
                </label>
                <input
                  className="form-control"
                  type="text"
                  placeholder="Descripsion de novedad"
                  name="descripcion_novedad"
                  onChange={handlechangeTate}
                  value={values?.descripcion_novedad}
                  readOnly={currentGuiaRegistro?.idGuia ? false : true}
                />
              </div>
            </div>
            <button
              type="button"
              className="btn btn-success w-100 mt-2"
              disabled={currentGuiaRegistro?.idGuia === undefined}
              onClick={addGuiaUpdate}
            >
              Guardar
            </button>
          </form>
        </div>
        {currentGuiaRegistro?.idGuia === undefined ? (
          <></>
        ) : (
          <div className="card card-body">
            <h5 className="titleSection-form">Detalle de Guia</h5>
            <hr />
            <EstructuraGuia
              guia={currentGuiaRegistro}
              showLine={false}
              showSpace={false}
              showMarca={false}
            />
          </div>
        )}

        <div className="card card-body">
          <div className="row align-items-center">
            <div className="col-sm-12 col-md-8">
              <h5 className="titleSection-form">Lista de Guias</h5>
            </div>
          </div>
          <hr />
          <div className="row align-items-start">
            <div className="row table-cm">
              <table className="table dt-responsive nowrap">
                <thead>
                  <tr className="header-table">
                    <th>ID</th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Estado"
                    >
                      Estado
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Fecha"
                    >
                      Fecha
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Origen"
                    >
                      Origen
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Destino"
                    >
                      Destino
                    </th>

                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Acciones"
                    >
                      Acciones
                    </th>
                  </tr>
                </thead>

                <tbody>
                  {/* {listTextGuias?.map((guia) => ( */}
                  {listGuiasRegistro?.map((guia) => (
                    <tr key={guia?.idGuia} className="hover-table">
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={guia?.idGuia}
                      >
                        {guia?.idGuia}
                      </td>
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={
                          guia?.estado === null || guia?.estado === ""
                            ? "-----"
                            : guia?.estado
                        }
                      >
                        {guia?.estado === null || guia?.estado === ""
                          ? "-----"
                          : guia?.estado}
                      </td>
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={ParseDate(guia?.fechaActual)}
                      >
                        {ParseDate(guia?.fechaActual)}
                      </td>
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={`${guia?.remitente?.provincia}-${guia?.remitente?.canton}`}
                      >
                        {`${guia?.remitente?.provincia}-${guia?.remitente?.canton}`}
                      </td>
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={`${guia?.destinatario?.provincia}-${guia?.destinatario?.canton}`}
                      >
                        {`${guia?.destinatario?.provincia}-${guia?.destinatario?.canton}`}
                      </td>

                      <td className="text-truncate">
                        <a
                          className="actions btn-danger"
                          type="button"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Imprimir"
                          onClick={() => deleteGuia(guia?.idGuia)}
                        >
                          <i className="material-icons">delete_forever</i>
                        </a>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
              {listGuiasRegistro.length === 0 ? (
                <div>
                  <p>No hay items en la lista</p>
                </div>
              ) : (
                <></>
              )}
            </div>
          </div>
        </div>

        <div className="card card-body">
          <div className="row">
            <div className=" col mx-1"></div>

            <button
              type="button"
              className="btn btn-primary col mx-1"
              disabled={listGuiasRegistro.length === 0 ? true : false}
              onClick={actualizarGuiasDb}
            >
              {fetching ? <Spinner /> : "Dar de Baja"}
            </button>
          </div>
        </div>
      </div>
      <Dashfooter />
    </div>
  );
}

export default RegistroCorrespondencia;
