import React from "react";
import "../estructura_manifiesto/estructura-manifiesto.css";
import logo from "../../../img/new_logo.png";
import Barcode from "react-barcode";
import { ParseDate } from "../../../helpers/helper";

const EstructuraManifiesto = ({
  manifiesto,
  asignacion,
  last = false,
  showmarca = true,
}) => {
  return (
    <div className="manifiesto-page">
      {showmarca === true ? (
        <div className="marca-agua-m">
          <img className="img-marcaAgua-m" src={logo} alt="" />
        </div>
      ) : (
        <></>
      )}
      <div className="border-all top">
        <div className="row">
          <div className="col">
            <div className="logoManifiesto">
              <img src={logo} alt="" />
            </div>
          </div>
          <div className="col py-2 ">
            <div>
              <Barcode
                value={
                  manifiesto !== undefined
                    ? manifiesto.idManifiesto
                    : asignacion?.idAsignacion
                }
                width={2}
                height={40}
                displayValue={false}
                margin={0}
                marginTop={0}
              />
            </div>
            <b>
              {manifiesto !== undefined
                ? manifiesto.idManifiesto
                : asignacion?.idAsignacion}
            </b>
          </div>
        </div>
      </div>
      <div className="border-all">
        <div className="row ">
          <div className="col text-start container-info remove-m pt-2">
            <b className="separator-padding">Información</b>
            <div className="info-data ">
              <b className="separator-padding">Creado por: </b>

              {manifiesto !== undefined
                ? manifiesto.creadoPor
                : asignacion?.creadoPor}
            </div>
            <div className="info-data mb-2">
              <b className="separator-padding">Tipo Usuario: </b>
              {manifiesto !== undefined ? manifiesto.rol : asignacion?.rol}
            </div>
          </div>
          <div className="col text-start container-info  left remove-m pt-2">
            <b>
              Detalles de
              {manifiesto === undefined ? "Asignación" : "Manifiesto"}
            </b>
            <div className="info-data text-start">
              <b>Fecha:</b>
              {ParseDate(
                manifiesto !== undefined
                  ? manifiesto.fechaCreacion
                  : asignacion?.fechaCreacion
              )}
            </div>
            {manifiesto === undefined ? (
              <div className="info-data text-start mb-2">
                <b>Asignado A:</b> {asignacion?.asignadoA}
              </div>
            ) : (
              <div className="info-data text-start mb-2">
                <b>Destíno:</b> {manifiesto?.destino}
              </div>
            )}
          </div>
        </div>
      </div>
      <div className="text-start bottom left right-m separator-padding py-2">
        <b>Número de Items:</b>
        {manifiesto === undefined
          ? asignacion?.guiasAsignadas?.length
          : manifiesto?.listGuias?.length}
      </div>
      <div className="bottom left right-m">
        {manifiesto === undefined ? (
          <div className="row">
            <div className="col ">
              <b>Código de Barras</b>
            </div>
            <div className="col left">
              <b>Número Guía</b>
            </div>
            <div className="col left">
              <b>Peso Guía</b>
            </div>
            <div className="col left">
              <b>Firma</b>
            </div>
          </div>
        ) : (
          <div className="row">
            <div className="col ">
              <b>Número Guía</b>
            </div>
            <div className="col left">
              <b>Peso Guía</b>
            </div>
            <div className="col left">
              <b></b>
            </div>
          </div>
        )}
      </div>
      {manifiesto === undefined
        ? asignacion?.guiasAsignadas.map((item) => (
          <div key={item?.idGuia} className="bottom left right-m">
            <div className="row">
              <div className="col remove-m ">
                <div className="p-2">
                      <Barcode
                value={item?.idGuia}
                width={2}
                height={40}
                displayValue={false}
                margin={0}
                marginTop={0}
              />
                  </div>
              </div>
              <div className="col remove-m left">
                <div className="p-1">{item?.idGuia}</div>
              </div>
              <div className="col remove-m left">
                <div className="p-1">{item?.peso} Kg</div>
              </div>
              <div className="col remove-m left">
                <div className="p-1"></div>
              </div>
            </div>
          </div>
        ))
        : manifiesto?.listGuias?.map((item) => (
            <div key={item?.idGuia} className="bottom left right-m">
              <div className="row">
                <div className="col remove-m">
                  <div className="p-1">{item?.idGuia}</div>
                </div>
                <div className="col remove-m left">
                  <div className="p-1">{item?.peso} Kg</div>
                </div>
                <div className="col remove-m left">
                  <div className="p-1"></div>
                </div>
              </div>
            </div>
          ))}
      {last === true ? (
        <div className="separatorpage-last"></div>
      ) : (
        <div className="separatorpage"></div>
      )}
    </div>
  );
};

export default EstructuraManifiesto;
