import React from "react";
import "../recepcion_manifiestos/recepcion_manifiestos.css";
import { DebounceInput } from "react-debounce-input";
import RecepcionManifiestoHook from "../../../../hooks/recepcion_manifiesto_hook";
import Spinner from "../../../spinner/spinner";
import { ParseDate } from "../../../../helpers/helper";
import EstructuraManifiesto from "../../../dash-componets/estructura_manifiesto/estructura-manifiesto";
import Dashfooter from "../../../dash-componets/footter/dashfooter";

function RecepcionManifiestos() {
  const [
    values,
    fetching,
    currentManifiesto,
    numRecibidos,
    numMalEncaminados,
    searchIdReseptarGuia,
    numPendientes,
    actualizarManifiestoGuiaDB,
    handlerSearchText,
    searchManifiesto,
    actualizarEstadoGuia,
    setIdGuiaSearch,
  ] = RecepcionManifiestoHook();

  return (
    <div>
      <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4 scroll-section">
        <div className="container-fluid pt-4 d-print-none">
          <h4 className="title-separtor">Recepción de Manifiestos</h4>
          <hr />
          <div className="card card-body">
            <div className="row ">
              <div className="col-lg-6 ">
                <form>
                  <div className="input-group">
                    <label className="form-label-input">
                      Buscar Manifiesto
                    </label>
                    <DebounceInput
                      minLength={0}
                      debounceTimeout={350}
                      type="text"
                      className="form-control dropdown-toggle"
                      placeholder="Num. de Manifiesto..."
                      name="idManifiesto"
                      value={values?.idManifiesto}
                      onChange={searchManifiesto}
                    />
                    <span className="mdi mdi-magnify search-icon" />
                    {fetching ? (
                      <div className="container-spinner-buscar">
                        <Spinner />
                      </div>
                    ) : (
                      <button
                        className="btn-solid-secondary px-4 "
                        type="button"
                        onClick={searchManifiesto}
                      >
                        Buscar
                      </button>
                    )}
                  </div>
                </form>
              </div>
            </div>
            <h5 className="titleSection-form pt-4">Informacion de Contenido</h5>
            <hr />
            <div className="row ">
              <div className="col-sm-12 col-md-4">
                <form>
                  <div className="input-group">
                    <label className="form-label-input">Fecha Creación</label>
                    <input
                      type="text"
                      className="form-control"
                      placeholder={ParseDate(currentManifiesto?.fechaCreacion)}
                      readOnly
                    />
                  </div>
                </form>
              </div>

              <div className="col-sm-12 col-md-4">
                <form>
                  <div className="input-group">
                    <label className="form-label-input">Num. Envios</label>
                    <input
                      type="text"
                      className="form-control"
                      readOnly
                      placeholder={currentManifiesto?.listGuias?.length}
                    />
                  </div>
                </form>
              </div>
              <div className="col-sm-12 col-md-4">
                <form>
                  <div className="input-group">
                    <label className="form-label-input">
                      Provincia Destino
                    </label>
                    <input
                      type="text"
                      className="form-control"
                      readOnly
                      placeholder={currentManifiesto?.destino}
                    />
                  </div>
                </form>
              </div>
            </div>
            <h5 className="titleSection-form pt-4">Recepción por Oficina</h5>
            <hr />
            <div className="row ">
              <div className="col-sm-12 col-md-6">
                <form>
                  <div className="input-group">
                    <label className="form-label-input">Ingresar Guía</label>
                    <span className="input-group-text" id="basic-addon1">
                      <i className="material-icons">search</i>
                    </span>
                    <DebounceInput
                      minLength={0}
                      debounceTimeout={500}
                      type="text"
                      className="form-control dropdown-toggle"
                      placeholder="Num. de guia..."
                      name="idGuia"
                      value={searchIdReseptarGuia}
                      onChange={setIdGuiaSearch}
                      readOnly={currentManifiesto?.idManifiesto === undefined || currentManifiesto?.idManifiesto === ""? true:false}
                    />
                  </div>
                </form>
              </div>
            </div>
            <h5 className="titleSection-form pt-4">Comprobación de Envíos</h5>
            <hr />
            <div className="row ">
              <div className="col-sm-12 col-md-4">
                <form>
                  <div className="input-group">
                    <label className="form-label-input">Items Recibidos</label>
                    <input
                      type="text"
                      className="form-control"
                      value={numRecibidos}
                      readOnly
                    />
                  </div>
                </form>
              </div>

              <div className="col-sm-12 col-md-4">
                <form>
                  <div className="input-group">
                    <label className="form-label-input">Mal Encaminados</label>
                    <input
                      type="text"
                      className="form-control"
                      value={numMalEncaminados}
                      readOnly
                    />
                  </div>
                </form>
              </div>
              <div className="col-sm-12 col-md-4">
                <form>
                  <div className="input-group">
                    <label className="form-label-input">Pendientes</label>
                    <input
                      type="text"
                      className="form-control"
                      readOnly
                      value={numPendientes}
                    />
                  </div>
                </form>
              </div>
            </div>
          </div>
          <div className="card card-body">
            <div className="row align-items-center">
              <div className="col-sm-12 col-md-8">
                <h5 className="titleSection-form">Lista de Guias</h5>
              </div>
            </div>
            <hr />
            <div className="row align-items-start">
              <div className="row table-cm">
                <table className="table dt-responsive nowrap">
                  <thead>
                    <tr className="header-table">
                      <th>ID</th>
                      <th>Estado</th>
                      <th>Peso</th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentManifiesto?.listGuias?.map((guia) => {
                      return (
                        <tr key={guia.idGuia} className="hover-table">
                          <td>{guia.idGuia}</td>
                          <td>{guia.estado}</td>
                          <td>
                            <b>{guia.peso}</b> Kg
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
          <div className="card card-body">
            <div className="row">
              <button type="button" className="btn-dt col mx-1">
                Cancelar
              </button>
              <button
                type="button"
                onClick={actualizarManifiestoGuiaDB}
                className="btn btn-success col mx-1"
              >
                Guardar Datos
              </button>
            </div>
          </div>
        </div>
        <Dashfooter/>
      </div>
      <div className="col-md-9 ms-sm-auto col-lg-10 d-none d-print-block">
      {/* <div className="col-md-9 ms-sm-auto col-lg-10"> */}
        {<EstructuraManifiesto manifiesto={currentManifiesto} last={true} />}
      </div>
    </div>
  );
}

export default RecepcionManifiestos;
