import {
  deleteGuiaAsignacion,
  fingGuiaAsignacion,
  setAsignacionCorrespondencia,
  setNamedAsigned,
} from "../redux/actions/manifiestos_actions";
import { useToasts } from "react-toast-notifications";
import { useDispatch, useSelector } from "react-redux";
import { useState, useEffect } from "react";
import { generateId, removeSpaces } from "../helpers/helper";

function AsignacionManifiesto(initialState = "") {
  const dispatch = useDispatch();
  const { addToast } = useToasts();
  const [nameCartero, setnameCartero] = useState(initialState);

  const {
    manifiestos: {
      listGuiasAsignacion,
      fetching,
      msgError,
      nameAsigned,
      asignacionPrint,
    },
    auth: { dataProfile },
  } = useSelector((state) => state);

  const rolUser =
    dataProfile?.rol === "admin"
      ? "Administrador"
      : dataProfile?.rol === "puntoVenta"
      ? "Punto de venta"
      : dataProfile?.rol === "sucursal"
      ? "Sucursal"
      : "Corporativo";

  useEffect(() => {
    if (nameAsigned && nameAsigned !== "") {
      setnameCartero(nameAsigned);
    }
  }, [nameAsigned]);

  const updateCartero = (e) => {
    e.preventDefault();
    dispatch(setNamedAsigned(e.target.value));
    setnameCartero(e.target.value);
    // top-search
    setTimeout(() => {
      document.getElementById("top-search").focus();
    }, 450);
  };

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 2000,
      placement: "bottom-right",
      id: "d-print-none",
    });
  };

  const handlerSearchGuia = (e) => {
    e.preventDefault();
    const success = () => toastSms("Guia encontrada", "success");
    const error = (sms) => toastSms(`${sms}`, "error");
    let valueTemp = removeSpaces(e.target.value);

    let guiasEnLista = listGuiasAsignacion.filter((item) =>
      item.idGuia.includes(valueTemp)
    ).length;

    if (guiasEnLista === 1) {
      toastSms("Esta guia ya fue agregada ", "info");
      e.target.value = "";
    } else {
      if (nameCartero === "") {
        toastSms("debe seleccionar un encargado antes de agregar", "info");
      } else {
        if (valueTemp.trim()) {
          dispatch(fingGuiaAsignacion(valueTemp, error, success));
          setTimeout(() => {
            document.getElementById("top-search").focus();
          }, 600);
        } else {
          toastSms("El campo a buscar esta vacio", "info");
        }
      }
    }
  };
  const deletefromfromList = (guia) => {
    let listtemp = listGuiasAsignacion.filter(
      (guiaitem) => guiaitem.idGuia !== guia.idGuia
    );

    dispatch(deleteGuiaAsignacion(listtemp));
    toastSms("Guia eliminada de la lista", "info");
  };
  const setAsignaciondb = () => {
    const success = () =>
      toastSms("Asignacion creada correctamente", "success");
    const error = (sms) => toastSms(`${sms}`, "error");
    const printAsignacion = () => window.print();
    let tempAsignacion = generateAsignacion();

    dispatch(
      setAsignacionCorrespondencia(
        tempAsignacion,
        error,
        success,
        printAsignacion
      )
    );
  };

  const generateAsignacion = () => {
    let tempGuiaManifiesto = {
      estado: "creado",
      idAsignacion: generateId(),
      guiasAsignadas: [],
      fechaCreacion: Date.now().toString(),
      creadoPor: dataProfile?.email,
      rol: rolUser,
      asignadoA: nameAsigned,
    };
    listGuiasAsignacion.map((item) => {
      tempGuiaManifiesto = {
        ...tempGuiaManifiesto,
        guiasAsignadas: [
          ...tempGuiaManifiesto.guiasAsignadas,
          {
            estado: "creado",
            idGuia: item?.idGuia,
            peso: item?.peso.pesototal,
          },
        ],
      };
    });
    return tempGuiaManifiesto;
  };
  return [
    nameCartero,
    listGuiasAsignacion,
    asignacionPrint,
    fetching,
    setAsignaciondb,
    deletefromfromList,
    handlerSearchGuia,
    setnameCartero,
    updateCartero,
    generateAsignacion,
  ];
}

export default AsignacionManifiesto;
