import { useState } from "react";
import { useToasts } from "react-toast-notifications";
import { useDispatch, useSelector } from "react-redux";
import Swal from "sweetalert2";
import { v1 as uuid } from "uuid";
import {
  addGuiaManifiestos,
  addIdGuiaManifiesto,
  cleanManifiesto,
  crearManifiesto,
  deleteGuiaManifiesto,
  deleteguiaSearch,
  deleteIdGuiaManifiesto,
  findGuiaManifiesto,
  setDestion,
  setDestiono,
} from "../redux/actions/manifiestos_actions";

function CrearManifiestoHook(initialValues) {
  const {
    manifiestos: {
      listGuiasManifiesto,
      fetching,
      provinciaDestino,
      idGuiasManifiesto,
      manifiestoPrint,
    },
    auth: { dataProfile },
  } = useSelector((state) => state);
  const dispatch = useDispatch();
  const { addToast } = useToasts();

  const rolUser =
    dataProfile?.rol === "admin"
      ? "Administrador"
      : dataProfile?.rol === "puntoVenta"
      ? "Punto de venta"
      : dataProfile?.rol === "sucursal"
      ? "Sucursal"
      : "Corporativo";

  const [showErrorDestino, setshowErrorDestino] = useState(false);

  const swalWithBootstrapButtons = Swal.mixin({
    customClass: {
      confirmButton: "btn btn-success",
      cancelButton: "btn btn-danger",
    },
    buttonsStyling: false,
  });

  const showAlet = (provincia, guia) => {
    return swalWithBootstrapButtons
      .fire({
        title: "Quieres Agregar esta Guía?",
        text: `Esta guia pertenece a la provincia de ${provincia}, y el manifiesto a ${provinciaDestino} deseas agregarla a la lista`,
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "Agregar al Manifiesto",
        cancelButtonText: "No",
        reverseButtons: true,
      })
      .then((result) => {
        if (result.isConfirmed) {
          dispatch(addGuiaManifiestos(guia));
          toastSms("Guia Agregada", "success");
        }
      });
  };

  const showAletDelete = (guia) => {
    return swalWithBootstrapButtons
      .fire({
        title: "Estas seguro de eliminar esta guía?",
        text: `Esta guía se eliminará permanentemente de esta la lista, estas seguro`,

        showCancelButton: true,
        confirmButtonText: "Eliminar",
        cancelButtonText: "Cancelar",
        reverseButtons: true,
      })
      .then((result) => {
        if (result.isConfirmed) {
          dispatch(deleteGuiaManifiesto(guia.idGuia));
          toastSms("Guia eliminada", "error");
        }
      });
  };

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };
  const comprobarGuia = (provincia, guia) => {
    if (provinciaDestino === provincia) {
      dispatch(addGuiaManifiestos(guia));
      toastSms("Guia Agregada", "success");
    } else {
      showAlet(provincia, guia);
    }
  };
  const searchGuias = (e) => {
    e.preventDefault();
    if (provinciaDestino === "") {
      setshowErrorDestino(false);
      toastSms("Es necesario agregar un destino", "info");
    } else {
      if (idGuiasManifiesto.includes(e.target.value.trim())) {
        //cambiar
        toastSms("Esta guia ya fue agregada", "error");
      } else {
        const errorsms = (sms) => toastSms(`${sms}`, "error");
        dispatch(findGuiaManifiesto(e.target.value, errorsms, comprobarGuia));
      }
    }
  };
  const setDestinoReducer = (e) => {
    e.preventDefault();
    return dispatch(setDestion(e.target.value));
  };

  const deleteGuia = (guia) => {
    return showAletDelete(guia);
  };
  const printGuias = () =>{
    window.print();
  }
  const GuardarManifiesto = () => {
    let manifiesto = generateManifiesto();
    const errorsms = (sms) => toastSms(`${sms}`, "error");
    const successSms = (sms) => toastSms(`${sms}`, "success");
    dispatch(crearManifiesto(manifiesto, successSms, errorsms, printGuias));
  };

  const generateManifiesto = () => {
    let tempGuiaManifiesto = {
      estado: "creado",
      idManifiesto: initialValues.idManifiesto,
      listGuias: [],
      fechaCreacion: Date.now(),
      creadoPor: dataProfile?.email,
      rol: rolUser,
      destino: provinciaDestino,
    };
    listGuiasManifiesto.map((item) => {
      tempGuiaManifiesto = {
        ...tempGuiaManifiesto,
        listGuias: [
          ...tempGuiaManifiesto.listGuias,
          {
            estado: "creado",
            idGuia: item?.idGuia,
            peso: item?.peso.pesototal,
          },
        ],
      };
    });
    return tempGuiaManifiesto;
  };
  return [
    searchGuias,
    setDestinoReducer,
    deleteGuia,
    GuardarManifiesto,
    generateManifiesto,
    showErrorDestino,
    provinciaDestino,
    listGuiasManifiesto,
    fetching,
    manifiestoPrint,
  ];
}
export default CrearManifiestoHook;
