import {
  GET_LISTA_ROLES,
  GET_LISTA_ROLES_ERROR,
  GET_LISTA_ROLES_SUCCESS,
  UPDATE_ROL_FUNCTIONS,
  UPDATE_ROL_FUNCTIONS_ERROR,
  UPDATE_ROL_FUNCTIONS_SUCCESS,
  SET_NEW_ROLL,
  SET_NEW_ROLL_SUCCESS,
  SET_NEW_ROLL_ERROR,
} from "../../types/types";
import { db } from "../../firebase";

export const consultarRoles = () => {
  return async (dispatch) => {
    dispatch({
      type: GET_LISTA_ROLES,
    });
    try {
      db.collection("roles")
        .get()
        .then((resp) => {
          const listRoles = resp.docs.map((element) => {
            let temp = element.data();
            temp.id = element.id;
            return temp;
          });
          dispatch({
            type: GET_LISTA_ROLES_SUCCESS,
            payload: listRoles,
          });
        });
    } catch (error) {
      console.log("=====> Error Al buscar Roles");
      console.log(error);
      dispatch({
        type: GET_LISTA_ROLES_ERROR,
        payload: "Error al Buscar usuario",
      });
    }
  };
};
export const crearRol = ({ rolInfo, successSms, errorsms }) => {
  return async (dispatch) => {
    dispatch({
      type: SET_NEW_ROLL,
    });
    try {
      await db
        .collection("roles")
        .doc()
        .set({ rolInfo })
        .catch((error) => {
          dispatch({
            type: SET_NEW_ROLL_ERROR,
            payload: `Error al crear rol ${error}`,
          });
        });
      dispatch({
        type: SET_NEW_ROLL_SUCCESS,
      });
      successSms();
    } catch (error) {
      console.log(error);
      errorsms();
      dispatch({
        type: SET_NEW_ROLL_ERROR,
        payload: `Error al crear rol ${error}`,
      });
    }
  };
};

export const actualizarRol = (id, funciones, msgError, msgOk) => {
  return async (dispatch) => {
    dispatch({
      type: UPDATE_ROL_FUNCTIONS,
    });
    try {
      await db.collection("roles").doc(id).update({
        funciones,
      });
      dispatch(consultarRoles());
      dispatch({
        type: UPDATE_ROL_FUNCTIONS_SUCCESS,
      });
      msgOk();
    } catch (error) {
      console.log("====> Error al actualizar Rol");
      console.log(error);
      dispatch({
        type: UPDATE_ROL_FUNCTIONS_ERROR,
      });
      msgError("Error al actualizar funciones");
    }
  };
};
