import React, { useState } from "react";
import { useSelector } from "react-redux";
import "./estructuraPrint.css";
import logo from "../../img/new_logo.png";
import logoidCode from "../../img/idCodelogoPrint.png";
import { ParseDate } from "../../helpers/helper";
import Barcode from "react-barcode";
import QRCode from "qrcode.react";

const EstructuraGuia = ({
  guia,
  showLine = true,
  showSpace = false,
  showMarca = true,
}) => {
  return (
    <>
      <div className="guia-info">
        {showMarca === true ? (
          <div className="marca-agua ">
            <img className="img-marcaAgua " src={logo} alt="" />
          </div>
        ) : (
          <></>
        )}
        <div className="container-guia">
          <div className="row">
            <div className="col section-1">
              <div className="row header-info">
                <div className="col-6">
                  <div className="logoEmpresa text-start ">
                    <img src={logo} alt="" />
                  </div>
                </div>
                <div className="col-6 text-end guiaInformation remove-margin ">
                  <p className="item-info  remove-margin-bt">
                    Empresa Rapifas Courier Cía. Ltda.
                  </p>
                  <p className="item-info  remove-margin-bt">
                    RUC: 1191790010001
                  </p>
                  <p className="item-info  remove-margin-bt">www.rapifas.com</p>
                </div>
              </div>
              <div className="datos-remitente">
                <p className="creadopor remove-margin-bt text-start ">
                  <b>Creado por - </b>
                  <b>Rol: </b> {guia.creadoPor} <b>Usuario: </b>
                  {guia.creadoPorUsuario}
                </p>
                <div className="row">
                  <div className="col remove-margin remitente-container">
                    <div className="remitente text-start">
                      <div className="row remove-margin">
                        <div className="col-3 remitente-title remove-margin">
                          <span className="remitente-name">REMITENTE</span>
                        </div>
                        <div className="col-9 remove-margin set-mg">
                          <span className="rt-nm">
                            {guia?.remitente?.responsable.toUpperCase()}
                          </span>
                        </div>
                      </div>
                    </div>
                    <div className="row description-remitente p-2">
                      <div className="col-7 text-start remove-margin pdi-l ">
                        <p className="medium-size remove-margin-bt ">
                          {guia.remitente.razonSocial === "" ||
                          guia.remitente.razonSocial === null
                            ? "---------"
                            : guia?.remitente?.razonSocial.toUpperCase()}
                        </p>
                        <p className="medium-size remove-margin-bt ">
                          {guia?.remitente?.provincia.toUpperCase()}
                        </p>
                        <p className="medium-size remove-margin-bt ">
                          {guia.remitente.canton.toUpperCase()} - {guia.remitente.parroquia.toUpperCase()}
                        </p>
                        <p className="medium-size remove-margin-bt ">
                          {guia.remitente.direccion.toUpperCase()}
                        </p>
                        <p className="medium-size remove-margin-bt ">
                          {guia.remitente.referencia.toUpperCase()}
                        </p>
                      </div>
                      <div className="col-5 text-start remove-margin">
                        <p className="medium-size remove-margin-bt ">
                          <b>RUC/CI:</b> {guia.remitente.ruc.toUpperCase()}
                        </p>
                        <p className="medium-size remove-margin-bt ">
                          <b>Teléf:</b> {guia.remitente.telefonoEmpresa.toUpperCase()}
                        </p>
                        <p className="medium-size remove-margin-bt ">
                          <b>Teléf 2:</b> {guia.remitente.telefonoResponsable.toUpperCase()}
                        </p>
                        <p className="medium-size remove-margin-bt ">
                          <b>Cód. Postal:</b> {guia.remitente.codigoPostal.toUpperCase()}
                        </p>
                      </div>
                      <p className="medium-size remove-margin-bt text-start correo-remitente">
                        <b>Correo:</b>{" "}
                        {guia.remitente.email === ""
                          ? "---------"
                          : guia.remitente.email}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
              <div className="row remove-margin ">
                <div className="col sec-2 remove-margin">
                  <div className="left right-m px-1">
                    <div className="row medium-size">
                      <div className="col-6 text-start">
                        <b>Tipo: </b>
                      </div>
                      <div className="col-6 text-end size-tipo">
                        {guia?.tipo.toUpperCase()}
                      </div>
                    </div>
                  </div>
                  <div className="caracteristicas-envio">
                    <div className="titleCaracteristicas top peso-text-size">Peso</div>
                    <div className="info-caracteristicas">
                      <div className="row section-vol-pes remove-margin">
                        <div className="col text-start remove-margin psv p-1 pesovol-text">
                          PesoVol.
                        </div>
                        <div className="col text-vol remove-margin alt-vl p-1">
                          <span>Alto</span>
                        </div>
                        <div className="col text-vol remove-margin ang-ps p-1">
                          Ancho
                        </div>
                        <div className="col text-vol remove-margin lg-v p-1">
                          Largo
                        </div>
                      </div>
                      <div className="row p-1">
                        <div className="col-6 text-start medium-size-cv py-1">
                          Peso (Kg).
                        </div>
                        <div className="col-6 text-end peso-i">
                          {guia.peso.pesototal === ""
                            ? ""
                            : guia.peso.pesototal}
                          Kg
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="title-valores-section">
                    <div className="row ">
                      <div className="col title-valores text-center v-mercaderia">
                        Valor Declarado
                      </div>
                      <div className="col title-valores border-doted text-center">
                        Valor <br />
                        Asegurado
                      </div>
                      <div className="col title-valores text-cente v-piezas">
                        Piezas
                      </div>
                    </div>
                  </div>
                  <div className="row ">
                    <div className="col medium-size v-mercaderia">
                      $ {guia.v_declarado}
                    </div>
                    <div className="col medium-size">
                      $ {guia.v_asegurado === "" ? "0.00" : guia.v_asegurado}
                    </div>
                    <div className="col medium-size v-piezas-v">
                      {guia.numPiezas}
                    </div>
                  </div>
                  <div className="detalles-title">Descripción de el Envío</div>
                  <div className="description-section text-start text-break text-wrap">
                    {guia.contenido.toUpperCase()}
                  </div>
                </div>
                <div className="col sec-1 remove-margin">
                  <div className="caracteristicas-envio">
                    <div className="titleCaracteristicas">Costo de Envio</div>
                    <div className="info-caracteristicas p-1">
                      <div className="row">
                        <div className="col-6 text-start sized-text-csto">
                          <b>Sub Total:</b>
                        </div>
                        <div className="col-6 text-end sized-text-csto">
                          0.00
                        </div>
                      </div>
                      <div className="row">
                        <div className="col-6 text-start sized-text-csto">
                          <b>IVA (12%):</b>
                        </div>
                        <div className="col-6 text-end sized-text-csto">
                          0.00
                        </div>
                      </div>
                      <div className="row ">
                        <div className="col-6 text-start sized-text-csto ">
                          <b>Total:</b>
                        </div>
                        <div className="col-6 text-end sized-text-csto ">
                          {/* <div className="boder-doted text-end"></div> */}
                          <span>0.00</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="row remove-margin">
                {/* <div className="firma-rem"> */}
                <div className="col-6">
                  <img className="idCodeMarca" src={logoidCode} alt="" />
                </div>
                <div className="col-6">
                  <div className="line-firma"></div>
                  <p className="firma-description remove-margin-bt">
                    Firma Responsable.
                  </p>
                </div>
              </div>
            </div>
            <div className="col section-2">
              <div className="row remove-margin">
                <div className="col remove-margin">
                  <div className="user-tipe">{guia.creadoPor.toUpperCase()}</div>
                  <div className="fecha-envio">
                    <b>Fecha:</b> {ParseDate(guia.fechaActual)}
                    <div className="title-codigo-text text-end">
                      Codigo Rastreo:
                    </div>
                  </div>
                </div>
                <div className="col">
                  <div className="codigo-cont">
                    <Barcode
                      value={
                        guia.idGuia.length <= 10 ? guia.idGuia : "000000001"
                        // 1111001252
                      }
                      width={2.2}
                      height={40}
                      displayValue={false}
                      margin={0}
                      marginTop={0}
                    />
                  </div>
                  <div className="title-codigo">
                    {guia.idGuia.length <= 10 ? guia.idGuia : "000000001"}
                  </div>
                </div>
              </div>
              <div className="row destinatario-container remove-margin">
         
                <div className="col remove-margin">
                  <div className="cont-destinatario">
                    <div className="remitente text-start">
                      <div className="row remove-margin">
                        <div className="col-4 remitente-title remove-margin">
                          <span className="remitente-name">DESTINATARIO</span>
                          
                        </div>
                        <div className="col-8 remove-margin set-mg">
                          <span className="rt-nm">
                            {guia.destinatario.nombres.toUpperCase()}
                          </span>
                        </div>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-10 text-start data-local">
                        <div className="prv">
                          <b>Provincia: </b> {guia.destinatario.provincia.toUpperCase()}
                        </div>
                        <div className="cnt">
                          <b>Cantón: </b> {guia.destinatario.canton.toUpperCase()}
                        </div>
                        <div className="prq">
                          <b>Parroquia: </b> {guia.destinatario.parroquia.toUpperCase()}
                        </div>
                      </div>
                      <div className="col-2 remove-margin piezas-section">
                        <div className="title-piezas">Piezas</div>
                        <div className="num-piezas-section">
                          {guia.numPiezas > 1
                            ?  guia.fraccion === undefined? `${guia.numPiezas}` :`${guia.fraccion}/${guia.numPiezas}`
                            : `${guia.numPiezas}`}
                        </div>
                      </div>
                    </div>
                    <div className="info-destinatario text-start">
                      <div className="name-direccion padding-item-dest direccion-section">
                        <b>Dirección: </b> {guia.destinatario.direccion.toUpperCase()}
                      </div>
                      <div className="name-direccion padding-item-dest direccion-section">
                        <b>Referencia: </b> {guia.destinatario.referencia.toUpperCase()}
                      </div>
                      <div className="name-phone padding-item-dest">
                        <b>Teléfonos: </b> {guia.destinatario.telefono1.toUpperCase()} -{" "}
                        {guia.destinatario.telefono2.toUpperCase()}
                      </div>

                      <div className="name-postal padding-item-dest">
                        <b>Cod. Postal: </b> {guia.destinatario.codigoPostal.toUpperCase()}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="row remove-margin section-date">
                <div className="col title-fecha text-start remove-margin">
                  Fecha / Hora Entrega:
                </div>
                <div className="col text-center day p-1">Dia</div>
                <div className="col text-center month p-1">Mes</div>
                <div className="col text-center year p-1">Año</div>
                <div className="col text-center year p-1">HH:MM</div>
                {/* <div className="col text-center year">Min</div> */}
              </div>
              <div className="row">
                <div className="col-3  cont-qr p-1">
                  <div className="rste">Rastrear Envío</div>
                  <div className="qr-code">
                    <QRCode
                      value={
                        guia.idGuia.length <= 10 ? guia.idGuia : "000000001"
                      }
                      size={75}
                    />
                  </div>
                </div>
                <div className="col-9 remove-margin text-start cont-dt ">
                  <div className="name-rev p-1 py-2">
                    <b>Nombre:</b>
                  </div>
                  <div className="ruc-rev p-1">
                    <b>Cédula:</b>
                  </div>
                  <div className="firma-rev p-1">
                    <b>Firma/Sello:</b>
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* <div className="container-img"> */}
          {/* <img className="idCodeMarca" src={logoidCode} alt="" /> */}
          {showLine === false ? <></> : <div className="line-separator"></div>}
          {showSpace === true ? <div className="spacer"></div> : <></>}
        </div>
      </div>
    </>
  );
};

export default EstructuraGuia;
