import "../buscar_guias/buscar_guias.css";
import React, { useState } from "react";
import { useSelector } from "react-redux";

import { ParseDate } from "../../../../helpers/helper";
import Spinner from "../../../spinner/spinner";
import Dashfooter from "../../../dash-componets/footter/dashfooter";
import { UseFormDardeBajaGuias } from "../../../../hooks/user_from_buscar_guias_dardebaja";
import { listTextGuias } from "../../../../constants/constants";
import EstructuraGuia from "../../../estructura-guia-imprimir/estructura-print";

function BuscarGuias() {
  const [
    values,
    fetching,
    listGuias,
    CurrentGuia,
    darDeBaja,
    solicitarAnulacion,
    handlerChangeText,
    getGuiaSearch,
    showDetails,
    dowloadGuia,
    printGuiaAction,
    motivohandleChange,
    onPressEnter,
  ] = UseFormDardeBajaGuias();

  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4 scroll-section">
      <div className="container-fluid pt-4 d-print-none ">
        <h4 className="title-separtor">Rastrear Guía</h4>
        <hr />
        <div className="card card-body">
          <div className="row align-items-center mb-4">
            <div className="col-sm-12 col-md-6">
              <label htmlFor="simpleinput" className="form-label-input">
                Buscar Por
              </label>
              <select
                className="form-select"
                value={values?.busqueda}
                onChange={handlerChangeText}
                name="busqueda"
              >
                <option value="guia">Número de Guía</option>
                <option value="cedula_destinatario">
                  Cédula / RUC Destinatario
                </option>
                <option value="cedula_remitente">Cédula / RUC Remitente</option>
              </select>
            </div>

            <div className="col-sm-12 col-md-6">
              <form onSubmit={getGuiaSearch}>
                <label className="form-label-input">Buscar</label>
                <div className="input-group">
                  <input
                    type="text"
                    className="form-control dropdown-toggle"
                    placeholder="Buscar ..."
                    id="top-search"
                    name="textSearch"
                    value={values?.textSearch}
                    onChange={handlerChangeText}
                    autoFocus={true}
                  />
                  <span className="mdi mdi-magnify search-icon" />
                  {fetching ? (
                    <div className="container-spinner-buscar">
                      <Spinner />
                    </div>
                  ) : (
                    <button
                      className="btn-solid-secondary px-4 "
                      type="button"
                      onClickCapture={getGuiaSearch}
                    >
                      Buscar
                    </button>
                  )}
                </div>
              </form>
            </div>
          </div>
        </div>
        <div className="card card-body">
          <div className="col-sm-12 col-md-6">
            <h5 className="titleSection-form">Listado de Guias</h5>
          </div>
          <hr />
          <div className="row align-items-start">
            <div className="row table-cm table-responsive">
              <table className="table  nowrap">
                <thead>
                  <tr className="header-table">
                    <th>ID</th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Estado"
                    >
                      Estado
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Fecha"
                    >
                      Fecha
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Origen"
                    >
                      Origen
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Destino"
                    >
                      Destino
                    </th>

                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Acciones"
                    >
                      Acciones
                    </th>
                  </tr>
                </thead>

                <tbody>
                  {/* {listTextGuias?.map((guia) => ( */}
                  {listGuias?.map((guia) => (
                    <tr key={guia?.idGuia} className="hover-table">
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={guia?.idGuia}
                      >
                        {guia?.idGuia}
                      </td>
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={
                          guia?.estado === null || guia?.estado === ""
                            ? "-----"
                            : guia?.estado
                        }
                      >
                        {guia?.estado === null || guia?.estado === ""
                          ? "-----"
                          : guia?.estado}
                      </td>
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={ParseDate(guia?.fechaActual)}
                      >
                        {ParseDate(guia?.fechaActual)}
                      </td>
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={`${guia?.remitente?.provincia}-${guia?.remitente?.canton}`}
                      >
                        {`${guia?.remitente?.provincia}-${guia?.remitente?.canton}`}
                      </td>
                      <td
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title={`${guia?.destinatario?.provincia}-${guia?.destinatario?.canton}`}
                      >
                        {`${guia?.destinatario?.provincia}-${guia?.destinatario?.canton}`}
                      </td>

                      <td className="text-truncate">
                        <a
                          className="actions view"
                          type="button"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Ver Detalles"
                          onClick={(e) => showDetails(true, guia, e)}
                        >
                          <i className="material-icons">remove_red_eye</i>
                        </a>

                        <a
                          className="actions print"
                          type="button"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Imprimir"
                          onClick={() => printGuiaAction(guia)}
                        >
                          <i className="material-icons">local_printshop</i>
                        </a>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
              {listGuias.length === 0 ? (
                <div>
                  <p>No hay items en la lista</p>
                </div>
              ) : (
                <></>
              )}
            </div>
          </div>
          <hr />
        </div>

        <h4 className="title-separtor">Detalles</h4>
        <hr />

        {values?.shoDetail ? (
          <div className="form">
            {CurrentGuia?.idGuia === undefined ? (
              <></>
            ) : (
              <div className="card card-body">
                <div className="text-end mb-4">
                  <button
                    type="button"
                    class="btn-close "
                    aria-label="Close"
                    onClick={() => showDetails(false, null)}
                  ></button>
                </div>
                <EstructuraGuia
                  guia={CurrentGuia}
                  showLine={false}
                  showSpace={false}
                  showMarca={false}
                />
                <div className="row mt-4">
                  <div className="col">
                    <button
                      type="button"
                      className="col m-2 btn btn-danger w-100"
                      data-bs-toggle="modal"
                      data-bs-target="#anularguia"
                    >
                      Solicitar Anulación
                    </button>
                  </div>
                </div>
              </div>
            )}

            <div
              className="modal fade"
              id="anularguia"
              data-bs-backdrop="static"
              data-bs-keyboard="false"
              aria-labelledby="staticBackdropLabel"
              aria-hidden="true"
            >
              <div className="modal-dialog modal-dialog-centered">
                <div className="modal-content">
                  <div className="modal-header">
                    <h5 className="modal-title" id="staticBackdropLabel">
                      Solicitar Anulacion de guia
                    </h5>
                    <button
                      type="button"
                      className="btn-close"
                      data-bs-dismiss="modal"
                      aria-label="Close"
                    ></button>
                  </div>

                  <div className="modal-body">
                    <p>
                      Esta seguro de solicitar la anulacion de esta guia, una
                      ves solicitada no se podra cambiar este estado.
                    </p>
                    <input
                      type="text"
                      placeholder="ingrese el motivo de anulacion"
                      className="form-control"
                      onChange={motivohandleChange}
                      value={values?.motivoAnulacion}
                      name="motivoAnulacion"
                      required
                    />
                  </div>

                  <div className="modal-footer">
                    <button
                      type="button"
                      className="btn btn-secondary"
                      data-bs-dismiss="modal"
                      onClick={() => {
                        showDetails(false, null);
                      }}
                    >
                      Cancelar
                    </button>
                    <button
                      type="submit"
                      className=" btn btn-danger p-2"
                      data-bs-dismiss="modal"
                      onClick={solicitarAnulacion}
                    >
                      Enviar Solicitud
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ) : (
          <></>
        )}
      </div>
      <div
        className="modal fade"
        id="exampleModal"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Generar Guía
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">...</div>
            <div className="modal-footer">
              <button type="button" className="btn btn-primary">
                Guardar PDF
              </button>
              <button type="button" className="btn btn-secondary">
                Imprimir Guias
              </button>
            </div>
          </div>
        </div>
      </div>
      <Dashfooter />
      {/* showtoSalve
            ? "guias-print d-print-block"
            : "guias-print d-none d-print-block" */}
      {CurrentGuia?.remitente ? (
        <div
          id="current-guia-salve"
          className={
            values?.showDownload == true
              ? "p-1 mt-2 guias-print"
              : "p-1 mt-2 guias-print d-none d-print-block"
          }
        >
          <EstructuraGuia guia={CurrentGuia} />
          <EstructuraGuia guia={CurrentGuia} />
          <EstructuraGuia guia={CurrentGuia} />
        </div>
      ) : (
        <></>
      )}
    </div>
  );
}

export default BuscarGuias;
