import React from "react";
import { localidadesList } from "../../../../constants/db_provincias";
import { generateId, ParseDate } from "../../../../helpers/helper";
import "../crear-manifiestos/manifiestos.css";
import { v1 as uuid } from "uuid";
import CrearManifiestoHook from "../../../../hooks/crear_manifiestos_hook";
import { DebounceInput } from "react-debounce-input";
import Spinner from "../../../spinner/spinner";
import EstructuraManifiesto from "../../../dash-componets/estructura_manifiesto/estructura-manifiesto";
import Dashfooter from "../../../dash-componets/footter/dashfooter";
let initialValues = {
  idManifiesto: generateId(),
  fechaCreacionManifiesto: Date.now(),
  destino: "",
};
function crearManifiesto() {
  const [
    searchGuias,
    setDestinoReducer,
    deleteGuia,
    GuardarManifiesto,
    generateManifiesto,
    showErrorDestino,
    provinciaDestino,
    listGuiasManifiesto,
    fetching,
    manifiestoPrint,
  ] = CrearManifiestoHook(initialValues);

  let manifiestotemp = generateManifiesto();
  // let guias = [
  //maximo 35 guias

  // ];
  return (
    <div>
      <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4 scroll-section d-print-none">
        <div className="container-fluid pt-4 ">
          <h4 className="title-separtor">Crear Manifiestos</h4>
          <hr />
          <div className="card card-body">
            <div className="row mt-2">
              <div className="col-lg-6 ">
                <form>
                  <div className="mb-3">
                    <label className="form-label-input">Destino</label>
                    {listGuiasManifiesto.length === 0 ? (
                      <select
                        className="form-select"
                        id="example-select"
                        value={provinciaDestino}
                        onChange={setDestinoReducer}
                        name="destino"
                        required
                      >
                        <option value="">Seleccione un Destino</option>
                        {localidadesList?.map((e) => {
                          return (
                            <option
                              key={`destino_${e.provincia}`}
                              value={e.provincia}
                            >
                              {e.provincia}
                            </option>
                          );
                        })}
                      </select>
                    ) : (
                      <input
                        type="text"
                        name="destino"
                        className="form-control"
                        value={provinciaDestino}
                        readOnly
                      />
                    )}
                  </div>
                </form>
              </div>
              <div className="col-lg-6 ">
                <form>
                  <div className="input-group">
                    <label for="simpleinput" className="form-label-input">
                      Buscar Guias
                    </label>
                    <DebounceInput
                      minLength={0}
                      debounceTimeout={500}
                      type="text"
                      className="form-control dropdown-toggle"
                      placeholder="Num. Guia..."
                      onChange={searchGuias}
                      name="searchText"
                    />

                    {fetching ? (
                      <div className="container-spinner-buscar">
                        <Spinner />
                      </div>
                    ) : (
                      <button
                        className="btn-solid-secondary px-4 "
                        type="submit"
                        onClick={searchGuias}
                      >
                        Buscar
                      </button>
                    )}
                  </div>
                </form>
              </div>
            </div>
          </div>
          <div className="card card-body">
            <div className="row align-items-center">
              <div className="col-sm-12 col-md-8">
                <h5 className="titleSection-form">Lista de Guias</h5>
              </div>
            </div>
            <hr />
            <div className="row align-items-start">
              <div className="row table-cm">
                <table className="table dt-responsive nowrap">
                  <thead>
                    <tr className="header-table">
                      <th>ID</th>
                      <th>Fecha</th>
                      <th>Origen</th>
                      <th>Destino</th>
                      <th>Estado</th>
                      <th>Eliminar</th>
                      {/* <th /> */}
                    </tr>
                  </thead>

                  <tbody>
                    {listGuiasManifiesto?.map((guia) => {
                      return (
                        <tr
                          key={`origen_${guia.idGuia}`}
                          className="hover-table"
                        >
                          <td>{guia.idGuia}</td>
                          <td>{ParseDate(guia.fechaActual)}</td>
                          <td>
                            {`${guia.remitente.provincia}-${guia.remitente.canton}`}
                          </td>
                          <td>
                            {`${guia.destinatario.provincia}-${guia.destinatario.canton}`}
                          </td>
                          <td>{guia?.estado?.toString().toUpperCase()}</td>
                          <td>
                            <div
                              type="button"
                              className=" "
                              onClick={() => deleteGuia(guia)}
                            >
                              <i className="large material-icons">
                                delete_forever
                              </i>
                            </div>
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
                {listGuiasManifiesto.length === 0 ? (
                  <div className="no-Item">
                    <p className="text-muted noGuias">
                      No hay Guias Registradas
                    </p>
                  </div>
                ) : (
                  <></>
                )}
              </div>
            </div>
          </div>
          <div className="card card-body">
            <div className="row">
              {listGuiasManifiesto.length === 0 ? (
                <div
                  className="btn btn-success-disable col mx-1"
                  // type="button"
                >
                  Generar Manifiesto
                </div>
              ) : fetching ? (
                <div className="container-spinner-buscar col mx-1 w-100">
                  <Spinner />
                </div>
              ) : (
                <button
                  className="btn btn-success col mx-1"
                  type="button"
                  onClick={GuardarManifiesto}
                >
                  Generar Manifiesto
                </button>
              )}
            </div>
          </div>
        </div>
        <Dashfooter/>
      </div>
      
      {/* <div className="col-md-9 ms-sm-auto col-lg-10 d-none d-print-block"> */}
      <div className="col-md-9 ms-sm-auto col-lg-10 d-none d-print-block">
        {
          <EstructuraManifiesto
            manifiesto={manifiestoPrint?.idManifiesto === "" || manifiestoPrint?.idManifiesto === undefined?   manifiestotemp:  manifiestoPrint}
            last={true}
          />
        }
      </div>
    </div>
  );
}

export default crearManifiesto;
