import React, { useEffect, useState } from "react";
import "../slide-bar/slide-bar.css";
import { Link, useHistory } from "react-router-dom";
import Swal from "sweetalert2";
import { useDispatch, useSelector } from "react-redux";
import { cerrarSesion } from "../../../redux/actions/auth_actions";
// import {
//   setCurrentPage,
//   setSubCurrentPage,
// } from "../../../redux/actions/pages_actions";

function Slidebar() {
  const dispatch = useDispatch();
  const history = useHistory();
  const {
    auth: { funciones, dataProfile },
    // pages: { page, subPage },
  } = useSelector((state) => state);

  const [page, setPage] = useState(localStorage.getItem("page") ?? "");
  const [subPage, setSubPage] = useState(localStorage.getItem("subPage") ?? "");

  const { rol } = dataProfile;

  const verifyLogOut = async () => {
    console.log("Aqui");
    return Swal.fire({
      title: "¿Deseas Cerrar Sesión?",
      icon: "question",
      confirmButtonText: "Sí",
      showConfirmButton: true,
      cancelButtonText: "No",
      showCancelButton: true,
    }).then((e) => {
      if (e.isConfirmed) {
        dispatch(cerrarSesion(history));
      }
    });
  };
  const rolUser =
    rol === "admin"
      ? "Administrador"
      : rol === "puntoVenta"
        ? "Punto de venta"
        : rol === "sucursal"
          ? "Sucursal"
          : "Corporativo";

  const setNamePage = (pagename) => {
    localStorage.setItem("page", pagename === page ? "" : pagename);
    setPage(pagename === page ? "" : pagename);
  };

  const setSubNamePage = (pagename) => {
    localStorage.setItem("subPage", pagename);
    setPage(pagename);
  };

  return (
    <nav
      className="col-md-3 col-lg-2 d-md-block  sidebar d-print-none scroll-section-side-bar"
    >
      <div className="position-sticky container-nenu">
        <ul className="nav flex-column align-items-start">
          <li className="nav-item pb-2">
            <a
              className="title-rol link-light"
              aria-current="page"
              href="/dashboard/home"
            >
              {/* //TODO nombre del rol */}
              <span>
                <span className="rol-menu">{rolUser}</span>{" "}
              </span>
            </a>
          </li>

          <li className="nav-item">
            <a
              className="nav-link link-light"
              // href="#"
              data-bs-toggle="collapse"
              href="#collapseExample"
              role="button"
              aria-expanded="false"
              aria-controls="collapseExample"
              onClickCapture={() => setNamePage("guias")}
            >
              <div className={`${page === "guias" ? "row selected" : "row"}`}>
                <div className="col-1">
                  <i
                    className={`${page === "guias"
                      ? " selected material-icons text-start"
                      : "material-icons text-start"
                      }`}
                  //  className="material-icons text-start"
                  >
                    assignment
                  </i>
                </div>
                <div className="col text-start align-middle title-Text mt-1">
                  Guías
                </div>
              </div>
            </a>
          </li>
          <div
            className={`collapse w-100 align-items-start   ${page === "guias" ? "show" : ""
              }`}
            id="collapseExample"
          >
            <div className="list-group-flush list-items">
              {funciones?.g_crear ? (
                <Link to="/dashboard/crearGuia" className="ref-link">
                  <button
                    type="button"
                    className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                    onClick={() => setSubNamePage("Crear Guias")}
                  >
                    <span
                      className={
                        subPage === "Crear Guias"
                          ? "text-btn-selected"
                          : "text-btn"
                      }
                    >
                      Crear Guia
                    </span>
                  </button>
                </Link>
              ) : (
                <></>
              )}
              {funciones?.g_buscar ? (
                <Link to="/dashboard/buscarGuias" className="ref-link">
                  <button
                    type="button"
                    className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                    onClick={() => setSubNamePage("Buscar Guias")}
                  >
                    <span
                      className={
                        subPage === "Buscar Guias"
                          ? "text-btn-selected"
                          : "text-btn"
                      }
                    >
                      Rastrear Envíos
                    </span>
                  </button>
                </Link>
              ) : (
                <></>
              )}
              {funciones?.g_anular ? (
                <Link to="/dashboard/anularGuia" className="ref-link">
                  <button
                    type="button"
                    className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                    onClick={() => setSubNamePage("Anulacion de Guias")}
                  >
                    <span
                      className={
                        subPage === "Anulacion de Guias"
                          ? "text-btn-selected"
                          : "text-btn"
                      }
                    >
                      Anular Guía
                    </span>
                  </button>
                </Link>
              ) : (
                <></>
              )}
            </div>
          </div>
          <li className="nav-item">
            <a
              className="nav-link link-light"
              href="#"
              data-bs-toggle="collapse"
              href="#Operaciones"
              role="button"
              aria-expanded="false"
              aria-controls="manifiestos"
              onClickCapture={() => setNamePage("Operaciones")}
            >
              <div
                className={`${page === "Operaciones" ? "row selected" : "row"}`}
              >
                <div className="col-1">
                  <i
                    className={`${page === "Operaciones"
                      ? " selected material-icons text-start"
                      : "material-icons text-start"
                      }`}
                  //  className="material-icons text-start"
                  >
                    bubble_chart
                  </i>
                </div>
                <div className="col text-start align-middle title-Text mt-1">
                  Logística y Operaciones
                </div>
              </div>
            </a>
          </li>
          <div
            className={`collapse w-100 align-items-start   ${page === "Operaciones" ? "show" : ""
              }`}
            id="Operaciones"
          >
            <div className="list-group-flush list-items">
              {/* {funciones?.m_crear ? ( */}
              <Link to="/dashboard/nopage" className="ref-link">
                <button
                  type="button"
                  className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                  onClick={() => setSubNamePage("Retorno de Guía")}
                >
                  <span
                    className={
                      subPage === "Retorno de Guía"
                        ? "text-btn-selected"
                        : "text-btn"
                    }
                  >
                    Retorno de Guía
                </span>
                </button>
              </Link>
              {/* </Link>
              ) : (
                <></>
              )} */}
              {/* {funciones?.m_receptar ? (
                <Link to="/dashboard/RecepcionManifiestos" className="ref-link"> */}
              {/* <button
                type="button"
                className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                onClick={() => setSubNamePage("Guía de Retiro")}
              >
                <span
                  className={
                    subPage === "Guía de Retiro"
                      ? "text-btn-selected"
                      : "text-btn"
                  }
                >
                  Guía de Retiro
                </span>
              </button> */}
              <Link to="/dashboard/nopage" className="ref-link">
                <button
                  type="button"
                  className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                  onClick={() => setSubNamePage("Guía de Retiro")}
                >
                  <span
                    className={
                      subPage === "Guía de Retiro"
                        ? "text-btn-selected"
                        : "text-btn"
                    }
                  >
                    Guía de Retiro
                  </span>
                </button>
              </Link>
              <Link to="/dashboard/nopage" className="ref-link">
                <button
                  type="button"
                  className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                  onClick={() => setSubNamePage("Formulario para Seguro")}
                >
                  <span
                    className={
                      subPage === "Formulario para Seguro"
                        ? "text-btn-selected"
                        : "text-btn"
                    }
                  >
                    Formulario para Seguro
                </span>
                </button>
              </Link>
              {/* </Link>
              ) : (
                <></>
              )} */}
            </div>
          </div>
          
         { rolUser ==="Corporativo" ||rolUser ==="Punto de venta" ?  <></>: <li className="nav-item">
            <a
              className="nav-link link-light"
              // href="#"
              data-bs-toggle="collapse"
              // href="#manifiestos"
              role="button"
              aria-expanded="false"
              aria-controls="manifiestos"
              onClickCapture={() => setNamePage("manifiestos")}
            >
              <div
                className={`${page === "manifiestos" ? "row selected" : "row"}`}
              >
                <div className="col-1">
                  <i
                    className={`${page === "manifiestos"
                      ? " selected material-icons text-start"
                      : "material-icons text-start"
                      }`}
                  //  className="material-icons text-start"
                  >
                    dvr
                  </i>
                </div>
                <div className="col text-start align-middle title-Text mt-1">
                  Manifiestos
                </div>
              </div>
            </a>
          </li>}
          <div
            className={`collapse w-100 align-items-start   ${page === "manifiestos" ? "show" : ""
              }`}
            id="manifiestos"
          >
            <div className="list-group-flush list-items">
              {funciones?.g_actualizarPeso ? (
                // TODO modificar funcion para manifiestos
                <Link to="/dashboard/actualizarPesos" className="ref-link">
                  <button
                    type="button"
                    className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                    onClick={() => setSubNamePage("Actualizacion de Pesos")}
                  >
                    <span
                      className={
                        subPage === "Actualizacion de Pesos"
                          ? "text-btn-selected"
                          : "text-btn"
                      }
                    >
                      Actualizar Peso
                    </span>
                  </button>
                </Link>
              ) : (
                <></>
              )}
              {funciones?.m_crear ? (
                <Link to="/dashboard/manifiestos" className="ref-link">
                  <button
                    type="button"
                    className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                    onClick={() => setSubNamePage("Crear_Manifiestos")}
                  >
                    <span
                      className={
                        subPage === "Crear_Manifiestos"
                          ? "text-btn-selected"
                          : "text-btn"
                      }
                    >
                      Crear Manifiesto
                    </span>
                  </button>
                </Link>
              ) : (
                <></>
              )}
              {funciones?.m_receptar ? (
                <Link to="/dashboard/RecepcionManifiestos" className="ref-link">
                  <button
                    type="button"
                    className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                    onClick={() => setSubNamePage("Recepción de Manifiestos")}
                  >
                    <span
                      className={
                        subPage === "Recepción de Manifiestos"
                          ? "text-btn-selected"
                          : "text-btn"
                      }
                    >
                      Recepción de Manifiesto
                    </span>
                  </button>
                </Link>
              ) : (
                <></>
              )}
              {funciones?.m_receptar ? (
                <Link to="/dashboard/buscarManifiesto" className="ref-link">
                  <button
                    type="button"
                    className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                    onClick={() => setSubNamePage("Buscar Manifiestos")}
                  >
                    <span
                      className={
                        subPage === "Buscar Manifiestos"
                          ? "text-btn-selected"
                          : "text-btn"
                      }
                    >
                      Buscar Manifiestos
                    </span>
                  </button>
                </Link>
              ) : (
                <></>
              )}
              {funciones?.m_asignar ? (
                <Link
                  to="/dashboard/AsignacionCorrespondencia"
                  className="ref-link"
                >
                  <button
                    type="button"
                    className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                    onClick={() =>
                      setSubNamePage("Asignación de Correspondencia")
                    }
                  >
                    <span
                      className={
                        subPage === "Asignación de Correspondencia"
                          ? "text-btn-selected"
                          : "text-btn"
                      }
                    >
                      Asignación a Cartero
                    </span>
                  </button>
                </Link>
              ) : (
                <></>
              )}
              {funciones?.m_asignar ? (
                <Link to="/dashboard/buscarAsignacion" className="ref-link">
                  <button
                    type="button"
                    className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                    onClick={() =>
                      setSubNamePage("Buscar Asignacion de Correspondencia")
                    }
                  >
                    <span
                      className={
                        subPage === "Buscar Asignacion de Correspondencia"
                          ? "text-btn-selected"
                          : "text-btn"
                      }
                    >
                      Buscar Asignacion de Correspondencia
                    </span>
                  </button>
                </Link>
              ) : (
                <></>
              )}
              {funciones?.m_registrar ? (
                <Link
                  to="/dashboard/registroCorrespondencia"
                  className="ref-link"
                >
                  <button
                    type="button"
                    className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                    onClick={() =>
                      setSubNamePage("Registro de Entrega de Correspondencia")
                    }
                  >
                    <span
                      className={
                        subPage === "Registro de Entrega de Correspondencia"
                          ? "text-btn-selected"
                          : "text-btn"
                      }
                    >
                      Registro de Correspondencia
                    </span>
                  </button>
                </Link>
              ) : (
                <></>
              )}
            </div>
          </div>
          {funciones?.u_buscar || funciones?.u_crear ? (
            <>
              <li className="nav-item">
                <a
                  className="nav-link link-light"
                  href="#"
                  data-bs-toggle="collapse"
                  href="#gestionUsuarios"
                  role="button"
                  aria-expanded="false"
                  aria-controls="gestionUsuarios"
                  onClickCapture={() => setNamePage("gestionUsuarios")}
                >
                  <div
                    className={`${page === "gestionUsuarios" ? "row selected" : "row"
                      }`}
                  >
                    <div className="col-1">
                      <i
                        className={`${page === "gestionUsuarios"
                          ? " selected material-icons text-start"
                          : "material-icons text-start"
                          }`}
                      //  className="material-icons text-start"
                      >
                        people
                      </i>
                    </div>
                    <div className="col text-start align-middle title-Text mt-1">
                      Gestión de Usuarios
                    </div>
                  </div>
                </a>
              </li>
              <div
                className={`collapse w-100 align-items-start   ${page === "gestionUsuarios" ? "show" : ""
                  }`}
                id="gestionUsuarios"
              >
                <div className="list-group-flush list-items">
                  {funciones?.u_crear ? (
                    <Link to="/dashboard/crearUsuarios" className="ref-link">
                      <button
                        type="button"
                        className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                        onClick={() => setSubNamePage("Crear Usuarios")}
                      >
                        <span
                          className={
                            subPage === "Crear Usuarios"
                              ? "text-btn-selected"
                              : "text-btn"
                          }
                        >
                          Crear Usuarios
                        </span>
                      </button>
                    </Link>
                  ) : (
                    <></>
                  )}
                    {funciones?.u_crear ? (
                    <Link to="/dashboard/crearRolesUsuario" className="ref-link">
                      <button
                        type="button"
                        className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                        onClick={() => setSubNamePage("Crear Roles de Usuarios")}
                      >
                        <span
                          className={
                            subPage === "Crear Roles de Usuarios"
                              ? "text-btn-selected"
                              : "text-btn"
                          }
                        >
                          Crear Roles de Usuarios
                        </span>
                      </button>
                    </Link>
                  ) : (
                    <></>
                  )}
                  {funciones?.u_buscar ? (
                    <Link to="/dashboard/buscarusuarios" className="ref-link">
                      <button
                        type="button"
                        className="into-items-container  list-group-item list-group-item-action text-start btn-action"
                        onClick={() => setSubNamePage("Buscar Usuario")}
                      >
                        <span
                          className={
                            subPage === "Buscar Usuario"
                              ? "text-btn-selected"
                              : "text-btn"
                          }
                        >
                          Buscar Usuario
                        </span>
                      </button>
                    </Link>
                  ) : (
                    <></>
                  )}
                </div>
              </div>
            </>
          ) : (
            <></>
          )}

          {funciones?.p_asignacion ? (
            <li className="nav-item">
              <a
                className="nav-link link-light"
                href="#"
                data-bs-toggle="collapse"
                href="#GestionPlataforma"
                role="button"
                aria-expanded="false"
                aria-controls="GestionPlataforma"
                onClickCapture={() => setNamePage("GestionPlataforma")}
              >
                <div
                  className={`${page === "GestionPlataforma" ? "row selected" : "row"
                    }`}
                >
                  <div className="col-1">
                    <i
                      className={`${page === "GestionPlataforma"
                        ? " selected material-icons text-start"
                        : "material-icons text-start"
                        }`}
                    //  className="material-icons text-start"
                    >
                      content_paste
                    </i>
                  </div>
                  <div className="col text-start align-middle title-Text mt-1">
                    Gestión Plataforma
                  </div>
                </div>
              </a>
            </li>
          ) : (
            <></>
          )}
          {funciones?.p_asignacion ? (
            <div
              className={`collapse w-100 align-items-start   ${page === "GestionPlataforma" ? "show" : ""
                }`}
              id="GestionPlataforma"
            >
              <div className="list-group-flush list-items">
                <Link to="/dashboard/AsignacionAcciones" className="ref-link">
                  <button
                    type="button"
                    className="into-items-container  list-group-item list-group-item-action text-start btn-action link-light"
                    onClick={() => setSubNamePage("Asignacion de Acciones")}
                  >
                    <span
                      className={
                        subPage === "Asignacion de Acciones"
                          ? "text-btn-selected"
                          : "text-btn"
                      }
                    >
                      Asignacion de Acciones
                    </span>
                  </button>
                </Link>
              </div>
            </div>
          ) : (
            <></>
          )}
          <button
            type="button"
            className="into-items-container list-group-item list-group-item-action text-start btn-action link-light pt-4"
            onClickCapture={() => verifyLogOut()}
          >
            <span className="text-btn-cerrar">Cerrar Sesion </span>
          </button>
        </ul>
      </div>
    </nav >
  );
}

export default Slidebar;
